import moment from 'moment';

const dateFormat = 'DD/MM/YYYY';

export const INITIAL_STATE = {
	requestId: -1,
	areas: ['Mina', 'Drilling'],
	requiredRgo: true,
	modalConfig: {},
	sector: undefined,
	description: null,
	requestNote: null,
	file: undefined,
	recommendedWells: [{ recommendation: null, north: null, east: null, elevation: null }],
	activities: [
		{
			activityName: null,
			responsible: null,
			from: moment().format(dateFormat),
			to: moment().format(dateFormat),
			observation: '',
		},
	],
	dateAuthorization: { from: moment().format(dateFormat), to: moment().add(1, 'days').format(dateFormat) },
	signatures: [
		{ area: 'Mina', responsibles: [{ personaName: '', signature: null, position: 0, signed: false }] },
		{ area: 'Drilling', responsibles: [{ personaName: '', signature: null, position: 0, signed: false }] },
	],
	rgo: {},
	platforms: [],
};

export function requestFormReducer(state = INITIAL_STATE, { type, payload }) {
	switch (type) {
		case '@requestForm/setActivities':
			return state.activities ? { ...state, activities: [...state.activities, payload] } : { ...state, activities: [payload] };
		case '@requestForm/setWell':
			return state.recommendedWells
				? { ...state, recommendedWells: [...state.recommendedWells, payload] }
				: { ...state, recommendedWells: [payload] };
		case '@requestForm/setModalConfig':
			return state.modalConfig ? { ...state, modalConfig: { ...state.modalConfig, ...payload } } : { ...state, modalConfig: { ...payload } };

		case '@requestForm/updateRootProp':
			const { prop, value } = payload;

			state[prop] = value;
			return { ...state };

		case '@requestForm/updateLogisticsArea':
			let finalAreas = [...payload];
			let signaturesToAdd = finalAreas.map((fa) => ({ area: fa, responsibles: [{ personaName: '', Signed: false, position: 0, Signature: '' }] }));
			return { ...state, areas: finalAreas, signatures: signaturesToAdd };

		case '@requestForm/updateSignatures':
			let { prop: propsignature, value: valuesignature, indexAreas: indexaressignature, indexResponsible: indexresponsiblesignature } = payload;
			let allsignatures = state.signatures;
			allsignatures[indexaressignature].responsibles[indexresponsiblesignature][propsignature] = valuesignature;

			return { ...state, signatures: allsignatures };

		// Actualizar propiedades de actividad
		case '@requestForm/updateActivity':
			const { prop: propactivity, value: valueactivity, index: indexactivity } = payload;
			let activities = state.activities;

			activities[indexactivity][propactivity] = valueactivity;
			return { ...state, activities };

		// Actualizar propiedades de pozos propuesto
		case '@requestForm/updateRecommendedWells':
			const { prop: propwell, value: valuewell, index: indexwell } = payload;
			let recommendedWells = state.recommendedWells;

			recommendedWells[indexwell][propwell] = valuewell;
			return { ...state, recommendedWells };

		case '@requestForm/updateDateAuthorization':
			const { prop: propdateauthorization, value: valuedateauthorization } = payload;
			let dateAuthorization = state.dateAuthorization;
			dateAuthorization[propdateauthorization] = valuedateauthorization;
			return { ...state, dateAuthorization };

		case '@requestForm/updateSignatureProps':
			const { areaIndex: areaindexupd, responsibleIndex: responsibleindexupd, prop: propupd, value: valueupd } = payload;
			let signArray = state.signatures;

			signArray[areaindexupd].responsibles[responsibleindexupd][propupd] = valueupd;
			return { ...state, signatures: signArray };

		case '@requestForm/addSignature':
			const { areaIndex: addAreaIndex, position: positionSignature } = payload;
			let signatures = state.signatures;

			signatures[addAreaIndex].responsibles = [
				...signatures[addAreaIndex].responsibles,
				{ personaName: '', signed: false, position: positionSignature, signature: '' },
			];
			return { ...state, signatures };

		case '@requestForm/removeWell':
			const { index: indexremovewell } = payload;
			let well = state.recommendedWells.filter((_, k) => k !== indexremovewell);

			return { ...state, recommendedWells: well };

		case '@requestForm/removeActivity':
			const { index: indexremoveactivity } = payload;
			let acts = state.activities.filter((_, k) => k !== indexremoveactivity);

			return { ...state, activities: acts };
		case '@requestForm/removeSignature':
			const { areaIndex: airemove, responsibleIndex: riremove } = payload;
			let signs = state.signatures;
			let responsiblesFilter = signs[airemove].responsibles.filter((_, k) => k !== riremove);

			signs[airemove].responsibles = responsiblesFilter;
			return { ...state, signatures: signs };
		case '@requestForm/updateAllData':
			return { ...payload };
		//PLATFORMS
		default:
			return state;
	}
}
