import { colors } from '@outlier-spa/mine/dist/config/themes';
import styled from 'styled-components';
import { theme } from 'theme';

export const TableStyles = styled.div`
	height: 100%;
	overflow: hidden;
	.ant-table {
		background: ${({ theme }) => theme.sectionBackground};
		color: ${({ theme }) => theme.fontColor};
	}
	.ant-table-thead > tr > th {
		background: ${({ theme }) => theme.sectionBackground};
		color: ${({ theme }) => theme.fontColor};
		border-color: ${({ theme }) => theme.fieldBorder};
	}

	.ant-table-tbody > tr > td {
		background: ${({ theme }) => theme.sectionBackground};
		color: ${({ theme }) => theme.fontColor};

		&.ant-table-column-sort {
			background: ${({ theme }) => theme.fieldDarkenBackground};
		}
	}

	.ant-table-thead th.ant-table-column-has-sorters:hover,
	.ant-table-thead th.ant-table-column-sort {
		background: ${({ theme }) => theme.fieldDarkenBackground};
	}

	.ant-table-tbody > tr.ant-table-row:hover > td {
		background: ${({ theme }) => theme.tableRowHover};
	}

	.ant-table-pagination.ant-pagination {
		border-color: ${({ theme }) => theme.fieldBorder};
	}

	.ant-table-column-sorter {
		color: ${({ theme }) => theme.fontColor};
	}

	.ant-table-column-sorter-up.active,
	.ant-table-column-sorter-down.active {
		color: ${({ theme }) => theme.activeBorder};
	}

	.ant-pagination-item {
		border-color: ${({ theme }) => theme.fieldBorder};
		color: ${({ theme }) => theme.fontColor};
		background-color: ${({ theme }) => theme.sectionBackground};
	}

	.ant-pagination-item:not(.ant-pagination-item-active) a {
		color: ${({ theme }) => theme.fontColor};
	}

	.ant-pagination-prev .ant-pagination-item-link,
	.ant-pagination-next .ant-pagination-item-link {
		border-color: ${({ theme }) => theme.fieldBorder};
		background-color: ${({ theme }) => theme.sectionBackground};
	}

	.ant-pagination-prev,
	.ant-pagination-next,
	.ant-pagination-jump-prev,
	.ant-pagination-jump-next {
		color: ${({ theme }) => theme.fontColor};
	}

	.ant-pagination-prev button,
	.ant-pagination-next button {
		color: ${({ theme }) => theme.fontColor};
	}

	.ant-table-body {
		scrollbar-color: transparent transparent;
		transition: scrollbar-color 1s cubic-bezier(0.19, 1, 0.22, 1);
		scrollbar-arrow-color: ${({ theme }) => theme.sectionBackground};
		scrollbar-track-color: ${({ theme }) => theme.sectionBackground};
		-ms-overflow-style: -ms-autohiding-scrollbar;
		height: ${(props) => props.tbodyHeight || 'auto'};
	}
	.ant-table-body:hover {
		scrollbar-color: ${({ theme }) => theme.activeBorder} transparent;
		transition: scrollbar-color 1s cubic-bezier(0.19, 1, 0.22, 1);
	}

	.ant-table-tbody > tr.ant-table-placeholder {
		.ant-empty-description {
			color: ${colors.softgray3};
		}

		.ant-empty-img-simple-g {
			stroke: ${colors.softgray3};
		}

		.ant-empty-img-simple-ellipse {
			fill: ${colors.softgray3};
		}

		.ant-empty-img-simple-path {
			fill: ${colors.softgray2};
		}

		&:hover > td {
			background: ${({ theme }) => theme.tableRowHover};
		}
	}

	.ant-table-cell-scrollbar {
		-webkit-box-shadow: 0 1px 0 1px ${({ theme }) => theme.fieldBorder};
		box-shadow: 0 1px 0 1px ${({ theme }) => theme.fieldBorder};
	}

	.ant-table-tbody > tr > td:not(:first-child):not(:only-child) {
		border-left: none;
	}

	.ant-table-tbody > tr:first-child > td:not(:first-child):not(:only-child) {
		/* :after {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        height: 100vh;
        width: 0;
        border-left: 1px solid ${({ theme }) => theme.fieldBorder};
        z-index: 2;
    } */
	}
`;

export const HepStyle = styled.div`
	.header-configuration-section,
	.header-details-section {
		.section-header {
			&::after {
				background-color: ${({ theme }) => theme.fieldBorder};
			}
		}
	}
`;

export const EmptyPlaceholder = styled.div`
	pointer-events: none;
	color: ${({ theme }) => theme.secondaryColor};
	font-weight: 400;
	font-size: 14px;
`;
