export const groupBy = (items, key) =>
	items.reduce(
		(result, item) => ({
			...result,
			[item[key]]: [...(result[item[key]] || []), item],
		}),
		{}
	);

export const toBase64 = async (file) =>
	new Promise((resolve, reject) => {
		const reader = new FileReader();
		reader.readAsDataURL(file);
		reader.onload = () => resolve(reader.result);
		reader.onerror = (error) => reject(error);
	});

export const urlToFile = async (url, filename, mimeType) => {
	return fetch(url)
		.then(function (res) {
			return res.arrayBuffer();
		})
		.then(function (buf) {
			return new File([buf], filename, { type: mimeType });
		});
};

export const b64toBlob = (b64Data, contentType = 'image/png', sliceSize = 512) => {
	const byteCharacters = atob(b64Data);
	const byteArrays = [];

	for (let offset = 0; offset < byteCharacters.length; offset += sliceSize) {
		const slice = byteCharacters.slice(offset, offset + sliceSize);

		const byteNumbers = new Array(slice.length);
		for (let i = 0; i < slice.length; i++) {
			byteNumbers[i] = slice.charCodeAt(i);
		}

		const byteArray = new Uint8Array(byteNumbers);
		byteArrays.push(byteArray);
	}

	const blob = new Blob(byteArrays, { type: contentType });
	return blob;
};

export function downloadFile({ data, filename = 'document', contentType = 'application/pdf' }) {
	let blob = new Blob(data, { type: contentType });
	let link = document.createElement('a');
	link.href = window.URL.createObjectURL(blob);
	link.download = filename;
	document.body.appendChild(link);
	link.click();
	document.body.removeChild(link);
}

export function resizeImage(file, maxSize = { w: 640, h: 480 }, outputType, callback) {
	var reader = new FileReader();
	reader.onload = function (readerEvent) {
		resize(readerEvent.target.result, maxSize, outputType, callback);
	};
	reader.readAsDataURL(file);
}

function resize(dataURL, maxSize, outputType, callback) {
	var image = new Image();
	image.onload = function (imageEvent) {
		var canvas = document.createElement('canvas'),
			width = image.width,
			height = image.height;
		if (width > height) {
			if (width > maxSize.w) {
				height *= maxSize.w / width;
				width = maxSize.w;
			}
		} else {
			if (height > maxSize.h) {
				width *= maxSize.h / height;
				height = maxSize.h;
			}
		}
		canvas.width = width;
		canvas.height = height;
		canvas.getContext('2d').drawImage(image, 0, 0, width, height);
		output(canvas, outputType, callback);
	};
	image.src = dataURL;
}

function output(canvas, outputType, callback) {
	switch (outputType) {
		case 'file':
			canvas.toBlob(
				function (blob) {
					callback(blob);
				},
				'image/jpeg',
				0.7
			);
			break;

		case 'dataURL':
			callback(canvas.toDataURL('image/png', 0.7));
			break;

		default:
			break;
	}
}
