export function PdfViewer({ url }) {
	return (
		<div className='pd-14' style={{ height: '80vh', width: '100%' }}>
			<object data={url} type='application/pdf' style={{ height: '100%', width: '100%' }}>
				<iframe src={`https://docs.google.com/viewer?url=${url}&embedded=true`} title='view'></iframe>
			</object>
		</div>
	);
}

export default PdfViewer;