import Loading from '@outlier-spa/loading';
import { DillFilled } from '@outlier-spa/mine-icon/dist/icons';
import { Section } from '@outlier-spa/mine/dist/components/Section';
import { CacheHEP, GetCacheHep, getCacheHoleDetails } from 'actions';
import { Table } from 'antd';
import { DetailsHeader } from 'components/Headers';
import _ from 'lodash';
import React, { useCallback, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { db } from 'utils/database';
import { _hepColumns, _hepDefaultTypes } from './AllHeps';
import { HepDetails } from './Details';
import { HepForm } from './Form';
import { HepStyle, TableStyles } from './styled';
import './styles.scss';

export function updateProp(prop, value) {
	return { type: '@hep/updateRootProps', payload: { prop: prop, value: value } };
}

export function updateHepProp(prop, value) {
	return { type: '@hep/updateHepData', payload: { prop, value } };
}

// * Obtiene los detalles del pozo, desde
function requestHoleDetails(hole, offline = false) {
	if (offline) {
		return getCacheHoleDetails(hole.holeId).then((response) => {
			var holeDetails = _.merge(hole, response);

			if (!holeDetails.type && holeDetails['Objetivo_Tipo']) {
				holeDetails.type = _hepDefaultTypes[holeDetails['Objetivo_Tipo']] || null;
			}

			return holeDetails;
		});
	}

	return db.get(`Hep/hole/${hole.holeId}`).then((response) => {
		if (response.data) {
			const { data } = response;
			var holeDetails = _.merge(hole, data);

			if (!holeDetails.type && holeDetails['Objetivo_Tipo']) {
				holeDetails.type = _hepDefaultTypes[holeDetails['Objetivo_Tipo']] || null;
			}
			
			return holeDetails;
		}
	});
}

function requestHoles(areaRequestId, dispatch, offline = false) {
	if (offline) {
		return GetCacheHep(areaRequestId).then((heps) => {
			dispatch(heps);
			dispatch({ type: '@base/updateRootProp', payload: { prop: 'hepSync', value: true } });
			return heps.payload.map((d) => ({ ...d, key: d['Nombre_Propuesto'] }));
		});
	}

	return db.get(`Hep/getByAreaRequest/${areaRequestId}`).then((response) => {
		if (response.data) {
			const { data } = response;

			// Guarda los datos en cache
			data.forEach((value) => {
				CacheHEP({ ...value, arearequestid: areaRequestId, inQueue: false }, areaRequestId)
					.then((resp) => {
						dispatch({ type: '@base/updateRootProp', payload: { prop: 'hepSync', value: true } });
					})
					.catch((err) => {
						dispatch({ type: '@base/updateRootProp', payload: { prop: 'hepSync', value: false } });
					});
			});

			return data.map((d) => ({ ...d, key: d['Nombre_Propuesto'] }));
		}
	});
}

function Hep(props) {
	const { areaRequestId } = props;
	const { holes, hepData } = useSelector((selector) => selector.hepReducer);
	const dispatch = useDispatch();
	const [loading, setLoading] = useState(false);
	const offline = useSelector((selector) => selector.baseReducer.offline);

	//* Busca los detalles del pozo seleccionado
	const handleChangeHoleId = (hole) => {
		if (!hole || !hole.holeId) return;
		setLoading(true);
		requestHoleDetails(hole, offline)
			.then((holeDetails) => {
				dispatch(updateProp('hepData', holeDetails));
			})
			.finally(() => setLoading(false));
	};

	const updateRootProps = useCallback(
		(prop, value) => {
			dispatch(updateProp(prop, value));
		},
		[dispatch]
	);

	const getAreaHoles = useCallback(() => {
		setLoading(true);

		requestHoles(areaRequestId, dispatch, offline)
			.then((data) => {
				updateRootProps('holes', data);
			})
			.finally(() => {
				setLoading(false);
			});
	}, [updateRootProps, dispatch]);

	useEffect(() => {
		dispatch({ type: 'clean' });
	}, []);

	useEffect(() => {
		if (areaRequestId === null) return;
		getAreaHoles(areaRequestId);
	}, [areaRequestId, getAreaHoles]);

	return (
		<HepStyle className='hep-page flex flex-column flex-1'>
			<Loading active={loading} />
			<DetailsHeader icon={<DillFilled style={{ transform: 'rotateZ(45deg)' }} />} text='Pozos propuestos' />
			<div className='flex flex-gap-14 flex-1' style={{ marginBottom: 14, height: 'calc(100% - 50px)', maxWidth: '100%' }}>
				<div className='flex-vgap-14 hep-page_holes'>
					<Section className='hep-page_list'>
						<TableStyles className='table-hep'>
							<Table
								columns={_hepColumns}
								dataSource={holes}
								pagination={false}
								scroll={{ x: '100%', y: 400 }}
								locale={{ triggerDesc: 'Descendente', triggerAsc: 'Ascendente', cancelSort: 'Cancelar orden' }}
								onRow={(item, _rowIndex) => {
									return {
										onClick: () => handleChangeHoleId(item),
									};
								}}
							/>
						</TableStyles>
					</Section>

					<Section className='hep-page_details'>
						<TableStyles>
							<HepDetails data={hepData.holeId ? [{ ...hepData, key: 1 }] : []} />
						</TableStyles>
					</Section>
				</div>

				<Section className='flex-gap-14 inline-fields' style={{ width: 288 }}>
					<HepForm onSave={() => getAreaHoles(areaRequestId)} />
				</Section>
			</div>
		</HepStyle>
	);
}

export default Hep;
