import Loading from '@outlier-spa/loading';
import { CheckOutlined, InfoFilled, LocationTone } from '@outlier-spa/mine-icon';
import Banner from '@outlier-spa/mine/dist/components/Banner';
import Button from '@outlier-spa/mine/dist/components/Button';
import Modal from '@outlier-spa/mine/dist/components/Modal';
import { BaseLayout } from 'App';
import Save from 'assets/Save';
import FixedHeader from 'components/Headers';
import _ from 'lodash';
import React, { useRef, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { db } from 'utils/database';
import RequestForm from './RequestForm';

function NewRequest(props) {
	const requestRef = useRef();
	const modalRef = useRef();
	const loadingRef = useRef();
	const history = useHistory();
	const [modalConfig, setModalConfig] = useState({});

	const handleOpenModal = () => {
		modalRef.current.open();
		setModalConfig({
			title: 'Confirmacin',
			okProps: { text: 'Crear' },
			content: 'Al hacer clic en "Crear", la informacin ya no se podr modificar.',
			onSubmit: () => handleSave(),
		});
	};

	const handleSave = async () => {
		const { active, setActive } = loadingRef.current;
		const { data, containError } = requestRef.current.getState();
		modalRef.current.close();

		if (containError) {
			setActive(false);
			return;
		}
		setActive(!active);

		var sendWithoutFile = _.cloneDeep(data);
		delete sendWithoutFile.file;

		var requestAreaResponse = await db.post('requestArea/SaveRequest', sendWithoutFile);
		var { state, id } = requestAreaResponse.data;

		if (state === true) {
			const formData = new FormData();
			formData.append('files[]', data.file);

			db.post(`requestArea/saveImage/${id}`, formData, {
				processData: false,
				headers: { 'Content-Type': 'multipart/form-data' },
			}).then(async (response) => {
				db.post(`requestArea/saveDocument/${id}`, sendWithoutFile).then((response) => {
					if (response.data) {
						setActive(!active);
						history.push(`/request-area-view/${id}`);
					}
				});
			});
		}
	};

	const handleRemove = () => {
		modalRef.current.open();
		setModalConfig({
			title: 'Confirmacin',
			content: 'Al presionar "Aceptar" se borrar la solicitud.',
			okProps: { text: 'Aceptar' },
			onSubmit: () => history.push('/request-area'),
		});
	};

	return (
		<>
			<Loading size={400} ref={loadingRef} />
			<Modal ref={modalRef} size='small' {...modalConfig} onSubmit={handleSave}>
				<div className='pd-14'>{modalConfig.content}</div>
			</Modal>
			<BaseLayout className='request-page ' onChangeTheme={props.onChangeTheme} light={props.light}>
				<FixedHeader
					className='sticky top0'
					left={<HeaderLeft />}
					right={<HeaderRight onRemove={handleRemove} onSave={handleOpenModal} />}></FixedHeader>
				<Banner>
					<InfoFilled style={{ marginRight: 8, fontSize: '0.875rem' }} />
					Solo puede firmar el documento luego de guardar los cambios con la informacin requerida.
				</Banner>
				<RequestForm ref={requestRef} mode='new'></RequestForm>
			</BaseLayout>
		</>
	);
}

export default NewRequest;

function HeaderLeft({ stockName = '' }) {
	return (
		<div className='header-left flex-gap-14' style={{ padding: '21px 0' }}>
			<LocationTone />
			<div>
				<h1>Nueva solicitud</h1>
				<span>{stockName}</span>
			</div>
		</div>
	);
}

function HeaderRight({ onRemove, onSave, allSignatures }) {
	return (
		<div className='header-right flex flex-middle'>
			<Button onClick={onRemove}>Cancelar</Button>
			{!allSignatures && (
				<Button type='green' prefix={<Save style={{ fontSize: 10 }} />} style={{ marginLeft: 14 }} onClick={onSave}>
					Crear solicitud
				</Button>
			)}
			{allSignatures && (
				<Button prefix={<CheckOutlined style={{ fontSize: 10 }} />} style={{ marginLeft: 14 }} type='primary' onClick={() => onSave('FINISH')}>
					Finalizar aprobacin
				</Button>
			)}
		</div>
	);
}
