import Loading from '@outlier-spa/loading';
import { InfoFilled, LocationTone } from '@outlier-spa/mine-icon';
import Banner from '@outlier-spa/mine/dist/components/Banner';
import Button from '@outlier-spa/mine/dist/components/Button';
import Modal from '@outlier-spa/mine/dist/components/Modal';
import { BaseLayout } from 'App';
import Save from 'assets/Save';
import FixedHeader from 'components/Headers';
import _ from 'lodash';
import moment from 'moment';
import { REQUEST_STATE } from 'pages/RequestArea/utils';
import React, { useCallback, useEffect, useReducer, useRef } from 'react';
import { useHistory } from 'react-router-dom';
import { INITIAL_STATE, newRequestExtensionReducer } from 'reducer/newRequestExtensionReducer';
import { db } from 'utils/database';
import '../RequestArea/styles.scss';
import RequestExtensionForm from './RequestExtensionForm';

function NewExtensionRequest(props) {
	const [state, dispatch] = useReducer(newRequestExtensionReducer, INITIAL_STATE);
	const { id: areaRequestId } = props.match.params;
	const extensionRef = useRef();
	const loadingRef = useRef();
	const modalRef = useRef();
	const history = useHistory();

	const updateRootProps = (prop, value) => {
		dispatch({ type: '@newExtension/updateRootProp', payload: { prop, value } });
	};

	const handleOpenModal = (value) => {
		modalRef.current.open();
		switch (value) {
			case 'save':
				dispatch({
					type: '@newExtension/setModalConfig',
					payload: {
						title: 'Crear solicitud de extensin',
						content: 'Realmente desea crear la solicitud de extensin.',
						size: 'small',
						okProps: { text: 'Crear solicitud' },
						onSubmit: () => handelSave(),
					},
				});
				return;
			case 'cancel':
				dispatch({
					type: '@newExtension/setModalConfig',
					payload: {
						title: 'Cancelar solicitud de extensin',
						content: 'Al presionar en "Aceptar" se descartar la solicitud de extensin',
						size: 'small',
						okProps: { text: 'Aceptar', type: 'danger' },
						onSubmit: () => {
							history.goBack();
						},
					},
				});
				return;
			default:
				return;
		}
	};

	const handelSave = async () => {
		const { data, containError } = extensionRef.current.getState();
		const signMeta = { current: 0, total: 0 };

		modalRef.current.close();
		updateRootProps('loading', true);

		if (containError) {
			updateRootProps('loading', false);
			return;
		}

		data.extensionSignatures.forEach((signature, index) => {
			signMeta['total'] += signature.responsibles.length;
			signMeta['current'] += signature.responsibles.filter((r) => r.signed).length;
		});

		data.extensionState = REQUEST_STATE.CREATED;

		if (signMeta.current === signMeta.total) {
			data.extensionState = REQUEST_STATE.APPROVED;
		}

		updateRootProps('loading', false);
		db.post('RequestExtension/save', data)
			.then(async (response) => {
				if (response.data) {
					history.push(`/request-area-view/${areaRequestId}`);
				}
			})
			.finally(() => {
				updateRootProps('loading', false);
			});
	};

	const findRequest = useCallback(() => {
		db.get(`RequestArea/getById?arearequestid=${areaRequestId}&showExtension=false&showReturn=false`).then((response) => {
			const { data } = response;
			updateRootProps('requestId', areaRequestId);
			updateRootProps('sector', data.sector);
			updateRootProps('startDate', data.dateAuthorization.to);
		});
	}, [areaRequestId]);

	useEffect(() => {
		findRequest();
	}, [findRequest]);

	return (
		<>
			<Loading ref={loadingRef} active={state.loading} />
			<Modal ref={modalRef} {...state.modalConfig}>
				<div className='pd-14 '>{state.modalConfig?.content}</div>
			</Modal>
			<BaseLayout cleanInner className='extension-request-page' style={{ padding: '0' }}>
				<FixedHeader
					className='sticky top0'
					left={<HeaderLeft name={state.sector} />}
					right={<HeaderRight onCancel={() => handleOpenModal('cancel')} onSubmit={() => handleOpenModal('save')} />}></FixedHeader>
				<Banner>
					<InfoFilled style={{ marginRight: 8, fontSize: '0.875rem' }} />
					Solo puede firmar el documento luego de guardar los cambios con la informacin requerida.
				</Banner>
				{state.startDate && <RequestExtensionForm ref={extensionRef} mode='new' startDate={moment(state.startDate, 'DD/MM/YYYY')} {...props} />}
			</BaseLayout>
		</>
	);
}

export default NewExtensionRequest;

function HeaderLeft({ name = '' }) {
	return (
		<div className='header-left flex-gap-14' style={{ padding: '21px 0' }}>
			<LocationTone />
			<div>
				<h1>Extensin de solicitud</h1>
				<span>{name}</span>
			</div>
		</div>
	);
}

function HeaderRight({ onCancel, onSubmit }) {
	return (
		<div className='header-right flex flex-middle'>
			<Button onClick={onCancel}>Cancelar</Button>
			<Button type='green' prefix={<Save style={{ fontSize: 10 }} />} onClick={onSubmit} style={{ marginLeft: 14 }}>
				Guardar Cambios
			</Button>
		</div>
	);
}
