import React from 'react';
import './styles.scss';
import cl from 'classnames';
import styled from 'styled-components';

const HeaderFixedStyles = styled.div`
	background-color: ${({ theme }) => theme.backgroundMain};
	.fixed-header-bottom-separator {
		background-color: ${({ theme }) => theme.fieldBorder};
	}
	.fixed-header-top-separator {
		background-color: ${({ theme }) => theme.fieldBorder};
	}
`;

function FixedHeader({ className, left, center, right, bottom, separatorPosition = 'bottom', showSeparator, ...rest }) {
	const fixedClassnames = cl('fixed-header', className);
	const separatorClassName = cl(`fixed-header-${separatorPosition}-separator`);
	return (
		<>
			<HeaderFixedStyles className={fixedClassnames} {...rest}>
				{showSeparator && <div className={separatorClassName}></div>}

				<div className='header-fixed-left'>{left}</div>
				<div className='header-fixed-center'>{center}</div>
				<div className='header-fixed-right'>{right}</div>
			</HeaderFixedStyles>
			{rest.children}
		</>
	);
}

export default FixedHeader;

export function DetailsHeader({ icon, text }) {
	return (
		<div className='header-details-small'>
			{icon}
			<span>{text}</span>
		</div>
	);
}

export function HeaderPage({ children, ...rest }) {
	return (
		<div style={{ padding: '8px 0' }} {...rest}>
			<span style={{ fontStyle: 'normal', fontWeight: 600, fontSize: 20, lineHeight: '34px' }}>{children}</span>
		</div>
	);
}
