import Loading from '@outlier-spa/loading';
import { DetailsOutlined, SaveTone } from '@outlier-spa/mine-icon';
import Banner from '@outlier-spa/mine/dist/components/Banner';
import Button, { BigIconButton } from '@outlier-spa/mine/dist/components/Button';
import Input, { TextArea } from '@outlier-spa/mine/dist/components/Input';
import Modal from '@outlier-spa/mine/dist/components/Modal';
import { Section, SectionHeader } from '@outlier-spa/mine/dist/components/Section';
import SwitchOptions from '@outlier-spa/mine/dist/components/SwitchOptions';
import { colors } from '@outlier-spa/mine/dist/config/themes';
import Save from 'assets/Save';
import Signature from 'assets/Signature';
import { TitleInput } from 'components/Common';
import DrawSignature, { defaultProps as defaultDrawSignatureProps } from 'components/DrawSignature';
import SignatureViewer from 'components/DrawSignature/SignatureViewer';
import FixedHeader, { DetailsHeader } from 'components/Headers';
import SimpleTableCheck from 'components/SimpleTableCheck';
import { REQUEST_STATE } from 'pages/RequestArea/utils';
import React, { useEffect, useReducer, useRef } from 'react';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { INITIAL_STATE, platformDeliveryReducer } from 'reducer/platformDeliveryReducer';
import { DescriptionText } from 'styled';
import { toBase64 } from 'utils';
import { db } from 'utils/database';
import indexDb from 'utils/db';
import { PlatformValidations } from './Validation';

const criticalControlOption = [{ content: 'Si' }, { content: 'No' }, { content: 'N/A' }];

function updateModal(payload) {
	return {
		type: '@platform/setModalConfig',
		payload: { ...payload },
	};
}

function updateSignatureProp({ areaIndex, responsibleIndex, prop, value }) {
	return { type: '@platform/updateSignatureProps', payload: { areaIndex: areaIndex, responsibleIndex: responsibleIndex, prop: prop, value: value } };
}

function updateLoading(active = true) {
	return { type: '@platform/loading', payload: active };
}

const PlatformDetails = React.forwardRef(({ sector, requestId, ...rest }, ref) => {
	const modalRef = useRef();
	const signatureRef = useRef();
	const loadingRef = useRef();
	const platformDetails = useSelector((selector) => selector.baseReducer);
	const offline = useSelector((selector) => selector.baseReducer.offline);
	const [details, dispatch] = useReducer(platformDeliveryReducer, { ...INITIAL_STATE, ...platformDetails.platformSelected });
	const history = useHistory();

	const handleOpenModalSave = (type) => {
		switch (type) {
			case 'savePlatform':
				modalRef.current.open();
				const contentOffline = 'Actualmente no esta conectado a internet, cuando se encuentre seal se sincronizara automaticamente';
				const contentOnline = 'Realmente desea guardar la entrega de plataforma.';
				dispatch(
					updateModal({
						title: 'Guardar entrega de plataforma',
						size: 'small',
						content: `${contentOnline}  ${offline ? contentOffline : ''}`,
						onSubmit: () => save(),
					})
				);
				break;

			default:
				break;
		}
	};

	const handleOpenSignatureModal = (areaIndex, responsibleIndex, prop) => {
		dispatch(
			updateModal({
				title: 'Firma',
				size: 'large',
				onSubmit: () => onSubmitModal(areaIndex, responsibleIndex, prop),
				content: (
					<>
						<div className='signature-draw-container'>
							<DrawSignature ref={signatureRef} {...defaultDrawSignatureProps} canvasWidth='100%' canvasHeight='80vh' />
						</div>
						<DescriptionText style={{ marginTop: 20 }}>
							La firma realizada en este recuadro, ser utilizada nicamente en este documento, y no ser guardada o manipulada fuera de este.
						</DescriptionText>
					</>
				),
			})
		);
		modalRef.current.open();
	};

	const onSubmitModal = (areaIndex, responsibleIndex, prop) => {
		signatureRef.current.getBase64Data(async function (value) {
			dispatch(updateSignatureProp({ areaIndex, responsibleIndex, prop, value: await toBase64(value) }));
		});

		dispatch(updateSignatureProp({ areaIndex, responsibleIndex, prop: 'signed', value: true }));
		modalRef.current.close();
	};

	const updateSignatureProps = (areaIndex, responsibleIndex, prop, value) => {
		dispatch(updateSignatureProp({ areaIndex, responsibleIndex, prop, value }));
	};

	const updateRevisionProp = (value, index) => {
		dispatch({ type: '@platform/updateRevisionProp', payload: { value, index } });
		checkRevisions();
	};

	const updateControlMeasureProp = (prop, value, index) => {
		dispatch({ type: '@platform/updateControlMeasureProp', payload: { prop, value, index } });
	};

	const updateCriticalControlProp = (value, index) => {
		dispatch({ type: '@platform/updateCriticalControlProp', payload: { value, index } });
	};

	const updateRootProps = (prop, value) => {
		dispatch({ type: '@platform/updateRootProps', payload: { prop, value } });
	};

	const save = async () => {
		const { data, containError } = check();
		const signMeta = { current: 0, total: 0 };
		const formData = new FormData();

		modalRef.current.close();
		updateLoading();

		if (containError) {
			updateLoading(false);
			return;
		}

		if (offline) {
			indexDb
				.table('platformDeliveryQueue')
				.add(JSON.parse(JSON.stringify(data)))
				.then((response) => {
					history.goBack();
				})
				.finally(() => {
					updateLoading(false);
				});

			return;
		}

		data.platformSignatures.forEach((signature, index) => {
			signMeta['total'] += signature.responsibles.length;
			signMeta['current'] += signature.responsibles.filter((r) => r.signed).length;
			signature.responsibles.forEach((responsible) => {
				var file = responsible.signature;
				formData.append(`file-${signature.area}-${index}-${responsible.position}-${responsible.personaName}`, file);
			});
		});

		data.state = signMeta.current === signMeta.total ? REQUEST_STATE.APPROVED : REQUEST_STATE.CREATED;

		db.post('PlatformDelivery/save', data, { contentType: 'application/json' })
			.then((resp) => {})
			.finally(() => {
				updateLoading(false);
				history.goBack();
			});
	};

	const checkRevisions = (error) => {
		var validation = PlatformValidations.checkRevision(details);

		dispatch({ type: '@platform/setControlMeasure', payload: validation.controlMeasures });
		updateRootProps('errors', validation.errors);
	};

	const check = () => {
		var validation = PlatformValidations.checkEmpty(details);

		updateRootProps('errors', validation.errors);
		return validation;
	};

	useEffect(() => {
		updateLoading();

		if (requestId) updateLoading(false);
		return () => updateLoading(false);
	}, []);

	return (
		<>
			<Loading ref={loadingRef} active={details.loading}></Loading>
			<Modal ref={modalRef} size='large' {...details.modalConfig}>
				<div className='pd-14 '>{details.modalConfig.content}</div>
			</Modal>
			<>
				<div className='platform-delivery-container'>
					<FixedHeader
						left={<HeaderLeft data={details} />}
						right={<HeaderRight onClick={handleOpenModalSave} showSave={!(details.state === REQUEST_STATE.APPROVED)} />}></FixedHeader>
					<div>
						<DetailsHeader icon={<DetailsOutlined />} text='Encabezado' />
						<div className='flex-vgap-20' style={{ marginBottom: 30 }}>
							<div>
								<TitleInput>Recomendacin</TitleInput>
								<Input
									disabled
									isError={details.errors && details.errors['recuperation']}
									message={details.errors && details.errors['recuperation']}
									size='large'
									placeholder='Ingrese recuperacin'
									value={details.recuperation}
									style={{ width: 340 }}
									onChange={(value) => {
										details.errors && value.trim() !== '' && delete details.errors['recuperation'];
										updateRootProps('recuperation', value);
									}}
								/>
							</div>
							<div>
								<TitleInput>DIP</TitleInput>
								<Input
									disabled
									isError={details.errors && details.errors['dip']}
									message={details.errors && details.errors['dip']}
									size='large'
									placeholder='Ingrese DIP'
									value={details.dip}
									style={{ width: 340 }}
									onChange={(value) => {
										details.errors && value.trim() !== '' && delete details.errors['dip'];
										updateRootProps('dip', value);
									}}
								/>
							</div>
							<div>
								<TitleInput>Azimuth</TitleInput>
								<Input
									disabled
									isError={details.errors && details.errors['azimuth']}
									message={details.errors && details.errors['azimuth']}
									size='large'
									placeholder='Ingrese azimuth'
									value={details.azimuth}
									style={{ width: 340 }}
									onChange={(value) => {
										details.errors && value.trim() !== '' && delete details.errors['azimuth'];
										updateRootProps('azimuth', value);
									}}
								/>
							</div>
							<div>
								<TitleInput>Profundidad programada</TitleInput>
								<Input
									disabled
									isError={details.errors && details.errors['depth']}
									message={details.errors && details.errors['depth']}
									size='large'
									placeholder='Ingrese profundidad programada'
									value={details.depth}
									style={{ width: 340 }}
									onChange={(value) => {
										details.errors && value.trim() !== '' && delete details.errors['depth'];
										updateRootProps('depth', value);
									}}
								/>
							</div>
							<div>
								<TitleInput>Sector</TitleInput>
								<Input
									disabled
									isError={details.errors && details.errors['sector']}
									message={details.errors && details.errors['sector']}
									size='large'
									placeholder='Ingrese sector'
									value={details.sector}
									style={{ width: 340 }}
									onChange={(value) => {
										details.errors && value.trim() !== '' && delete details.errors['sector'];
										updateRootProps('sector', value);
									}}
								/>
							</div>
						</div>

						{/* Revisin de plataforma de sondajes */}
						<DetailsHeader icon={<DetailsOutlined />} text='Revisin de plataforma de sondajes' />
						<div style={{ marginBottom: 30 }}>
							<SimpleTableCheck
								disabled={details.state === REQUEST_STATE.APPROVED}
								data={details.revisions}
								onChange={(revision) => {
									updateRevisionProp(revision.content, revision.elementPosition);
								}}
							/>
						</div>

						{details.controlMeasures && details.controlMeasures.length > 0 && (
							<>
								<Banner type='danger' style={{ marginBottom: 10 }}>
									<strong>Nota:</strong> Si alguna de las condiciones de las caractersticas de la plataforma NO CUMPLE se debe APLICAR las medidas de
									control para definirla como PLATAFORMA EXCEPCIONAL
								</Banner>

								{/* Medidas de control */}
								<DetailsHeader icon={<DetailsOutlined />} text='Medidas de control' />
								<Section style={{ marginBottom: 30 }}>
									<SectionHeader left='tems' />
									<div className='pd-14 flex-vgap-14'>
										{details.controlMeasures?.map((measure, key) => {
											return (
												<Section key={key} type='inner'>
													<SectionHeader left={`Item ${key + 1}`} />
													<div className='pd-14 flex-vgap-20'>
														<div>
															<TitleInput>Caracterstica plataforma faltante</TitleInput>
															<TextArea
																isError={details.errors && details.errors[`missingFeature${key}`]}
																message={details.errors && details.errors[`missingFeature${key}`]}
																size='large'
																disabled
																darken
																placeholder='Ingrese una caracteristica de plataforma faltante'
																value={measure.missingFeature}
																style={{ maxWidth: 860 }}
																onChange={(value) => {
																	details.errors && value.trim() !== '' && delete details.errors[`missingFeature${key}`];
																	updateControlMeasureProp('missingFeature', value, key);
																}}
															/>
														</div>
														<div>
															<TitleInput>Medidas de control</TitleInput>
															<TextArea
																disabled={details.state === REQUEST_STATE.APPROVED}
																isError={details.errors && details.errors[`controlMeasure${key}`]}
																message={details.errors && details.errors[`controlMeasure${key}`]}
																size='large'
																darken
																placeholder='Ingrese una medida de control'
																value={measure.controlMeasure}
																style={{ maxWidth: 860 }}
																onChange={(value) => {
																	details.errors && value.trim() !== '' && delete details.errors[`controlMeasure${key}`];
																	updateControlMeasureProp('controlMeasure', value, key);
																}}
															/>
														</div>
														<div>
															<TitleInput>Observaciones</TitleInput>
															<TextArea
																disabled={details.state === REQUEST_STATE.APPROVED}
																isError={details.errors && details.errors[`observation${key}`]}
																message={details.errors && details.errors[`observation${key}`]}
																size='large'
																darken
																placeholder='Ingrese una observacin'
																value={measure.observation}
																style={{ maxWidth: 860 }}
																onChange={(value) => {
																	details.errors && value.trim() !== '' && delete details.errors[`observation${key}`];
																	updateControlMeasureProp('observation', value, key);
																}}
															/>
														</div>
													</div>
												</Section>
											);
										})}
									</div>
								</Section>
							</>
						)}

						{/* Controles Crticos */}
						<DetailsHeader icon={<DetailsOutlined />} text='Controles Crticos' />
						<div className='flex-vgap-20' style={{ marginBottom: 30 }}>
							<div>
								<TitleInput>Requiere evaluacin geotcnica</TitleInput>
								<SwitchOptions
									disabled={details.state === REQUEST_STATE.APPROVED}
									size='large'
									index={details.requiresGeotechnicalEvaluation ? 0 : 1}
									onChange={(value) => {
										updateRootProps('requiresGeotechnicalEvaluation', value.content === 'Si');
									}}></SwitchOptions>
							</div>
							{details.requiredRgo && (
								<div>
									<TitleInput>R-GO</TitleInput>
									<Input
										disabled={details.state === REQUEST_STATE.APPROVED}
										value={details.rgoName}
										size='large'
										placeholder='Ingrese RGO'
										style={{ width: 340 }}></Input>
								</div>
							)}
							<div>
								<TitleInput>Riesgo Material</TitleInput>
								<Input
									disabled={details.state === REQUEST_STATE.APPROVED}
									isError={details.errors && details.errors['materialRisk']}
									message={details.errors && details.errors['materialRisk']}
									size='large'
									value={details.materialRisk}
									placeholder='Accidente a personas por falla de terreno'
									style={{ width: 340 }}
									onChange={(value) => {
										details.errors && value.trim() !== '' && delete details.errors['materialRisk'];
										updateRootProps('materialRisk', value);
									}}
								/>
							</div>
							<div style={{ marginBottom: 30 }}>
								<SimpleTableCheck
									disabled={details.state === REQUEST_STATE.APPROVED}
									data={details.criticalControl}
									options={criticalControlOption}
									headerProp={{ label: 'Control critico', secondaryLabel: 'Si / No / No Aplica' }}
									onChange={(control) => updateCriticalControlProp(control.content, control.elementPosition)}
								/>
							</div>
						</div>

						{/* Firmas Responsables */}
						<DetailsHeader icon={<Signature />} text='Firmas Responsables' />

						<div className='flex-vgap-20'>
							{details.platformSignatures?.map(({ area, responsibles }, areaIndex) => {
								return (
									<Section key={areaIndex} style={{ marginBottom: 20 }}>
										<SectionHeader left={`${area} plataforma de perforacin`}></SectionHeader>
										<div className='pd-14'>
											{responsibles.map(({ personaName, signed }, responsibleIndex) => {
												return (
													<Section type='inner' style={{ marginBottom: 14 }} key={responsibleIndex}>
														<div className='pd-14 signature-section'>
															<TitleInput>Nombres</TitleInput>
															<Input
																disabled={details.state === REQUEST_STATE.APPROVED || details.state === REQUEST_STATE.CREATED}
																isError={details.errors && details.errors[`personaName-${area}-${responsibleIndex}`]}
																message={details.errors && details.errors[`personaName-${area}-${responsibleIndex}`]}
																size='large'
																value={personaName}
																style={{ maxWidth: 560, marginBottom: 20 }}
																placeholder='Escriba nombres y apellidos'
																onChange={(value) => {
																	details.errors && value.trim() !== '' && delete details.errors[`personaName-${area}-${responsibleIndex}`];
																	updateSignatureProps(areaIndex, responsibleIndex, 'personaName', value);
																}}
															/>
															<TitleInput>Firma</TitleInput>
															<SignatureViewer
																disabled={signed && (details.state === REQUEST_STATE.APPROVED || details.state === REQUEST_STATE.CREATED)}
																signed={signed}
																onClick={() => handleOpenSignatureModal(areaIndex, responsibleIndex, 'signature')}
															/>
														</div>
													</Section>
												);
											})}
										</div>
									</Section>
								);
							})}
						</div>
					</div>
				</div>
			</>
			<div style={{ marginTop: 30, marginBottom: 60 }}>
				{details.state !== REQUEST_STATE.APPROVED && (
					<BigIconButton type='green' icon={<SaveTone />} onClick={() => handleOpenModalSave('savePlatform')}>
						Guardar Cambios
					</BigIconButton>
				)}
			</div>
		</>
	);
});

export default PlatformDetails;

function HeaderLeft({ data }) {
	const signatures = { current: 0, total: 0 };

	data.platformSignatures.forEach((signature) => {
		var responsibles = signature.responsibles;
		signatures.total += responsibles.length;
		signatures.current += responsibles.filter((responsible) => responsible.signed).length;
	});

	const color = signatures.current < signatures.total ? colors.red : colors.skyblue;

	return (
		<>
			<p>Formulario entrega de plataformas</p>
			<span style={{ color }}>
				{signatures.current}/{signatures.total} Firmas necesarias
			</span>
		</>
	);
}

// Acciones del lado derecho de la cabecera
function HeaderRight({ onClick, showSave = true }) {
	return (
		<div className='flex flex-center'>
			{showSave && (
				<Button type='green' prefix={<Save style={{ width: 14, height: 14 }} />} onClick={() => onClick && onClick('savePlatform')}>
					Guardar Cambios
				</Button>
			)}
		</div>
	);
}
