import { ClearFilled, VersionOutlined } from '@outlier-spa/mine-icon';
import * as d3 from 'd3';
import React, { useContext, useRef } from 'react';
import CanvasDraw from 'react-canvas-draw';
import { ThemeContext } from 'styled-components';

export const defaultProps = {
	lazyRadius: 1,
	brushRadius: 2,
	brushColor: '#0B22BD',
	catenaryColor: '#0B22BD',
	backgroundColor: '#2D2F35',
	canvasWidth: 665,
	canvasHeight: 338,
	hideGrid: true,
};

const DrawSignature = React.forwardRef(({ identifier, ...rest }, ref) => {
	const canvasDrawRef = useRef();
	const theme = useContext(ThemeContext);
	// const [draw, setDraw] = useState(null);
	const handleChange = (value) => {
		var lineGenerator = d3.line();
		let finalLines = [];

		const lines = JSON.parse(canvasDrawRef.current.getSaveData())['lines'];
		lines.forEach((line) => {
			const points = line.points;
			const finalPoint = [];
			points.forEach((point) => {
				finalPoint.push([point.x, point.y]);
			});
			finalLines.push(finalPoint);
		});

		var path = '';
		finalLines.forEach((fl) => {
			var pathData = lineGenerator(fl);
			path = path + `<path d='${pathData}' stroke='black'> </path>`;
		});

		let svgContainer = `<svg width='150px' height='45px' viewBox='0 0 ${rest.canvasWidth} ${rest.canvasHeight}' fill='none' xmlns='http://www.w3.org/2000/svg'>#PATHS</svg>`;
		svgContainer = svgContainer.replace('#PATHS', path);
		sessionStorage.setItem(identifier, svgContainer);
	};

	const getSvg = () => {};

	const getBase64Data = (callback = null, callbackUri = null) => {
		var uri = canvasDrawRef.current.canvas.drawing.toDataURL('image/png', 0.7);
		callbackUri && callbackUri(uri);
		callback &&
			canvasDrawRef.current.canvas.drawing.toBlob(async function (blob) {
				callback(blob);
			});
	};

	const handleUndo = () => {
		canvasDrawRef.current.undo();
	};
	const handleClear = () => {
		sessionStorage.removeItem(identifier);
		canvasDrawRef.current.clear();
	};
	ref && (ref.current = { getSvg, getBase64Data });

	return (
		<div style={{ position: 'relative' }}>
			<CanvasDraw ref={canvasDrawRef} onChange={handleChange} {...rest} backgroundColor={theme.dark ? '#2D2F35' : '#fff'} />
			<div className='signature-options-container'>
				<VersionOutlined onClick={handleUndo} />
				<ClearFilled style={{ marginLeft: 10 }} onClick={handleClear} />
			</div>
		</div>
	);
});

export default DrawSignature;
