import Loading from '@outlier-spa/loading';
import { DillFilled, ProbingTone } from '@outlier-spa/mine-icon';
import Modal from '@outlier-spa/mine/dist/components/Modal';
import { Section, SectionHeader } from '@outlier-spa/mine/dist/components/Section';
import Tag from '@outlier-spa/mine/dist/components/Tag';
import { colors } from '@outlier-spa/mine/dist/config/themes';
import { CachePlatforms, GetCachePlatform } from 'actions';
import Parachute from 'assets/Parachute';
import { DetailsHeader } from 'components/Headers';
import NotFound from 'components/NotFound';
import PdfViewer from 'components/PdfViewer';
import useDebouncedEffect from 'hooks/useDebouncedEffect';
import React, { useCallback, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Route, Switch, useHistory } from 'react-router-dom';
import { DescriptionText, Dot } from 'styled';
import { db } from 'utils/database';
import Details from './Details';
import './styles.scss';

const documentUrl = `${process.env.REACT_APP_API_URL}PlatformDelivery/getDocument/`;

function getPlatforms(requestId, dispatch, setLoading) {
	GetCachePlatform(requestId)
		.then((resp) => {
			dispatch(resp);
			dispatch({ type: '@base/updateRootProp', payload: { prop: 'platformDeliverySync', value: true } });
		})
		.catch((err) => {
			dispatch({ type: '@base/updateRootProp', payload: { prop: 'platformDeliverySync', value: false } });
		})
		.finally(() => {
			setLoading(false);
		});
}

function PlatformDelivery(props) {
	const { requestId } = props;
	const docRef = useRef();
	const history = useHistory();
	const dispatch = useDispatch();
	const offline = useSelector((selector) => selector.baseReducer.offline);
	const [loading, setLoading] = useState(false);
	const platformDelivery = useSelector((selector) => selector.baseReducer.platformDelivery);
	const [modalConfig, setModalConfig] = useState({ active: false });

	const findPlatforms = () => {
		setLoading(true);

		if (offline) {
			getPlatforms(requestId, dispatch, setLoading);
			return;
		}

		db.get(`PlatformDelivery/getByAreaRequest/${requestId}`)
			.then((response) => {
				if (response.data) {
					const { data } = response;
					// Guarda los datos en local
					data.forEach((value) => {
						CachePlatforms(value)
							.then((resp) => {
								dispatch({ type: '@base/updateRootProp', payload: { prop: 'platformDeliverySync', value: true } });
							})
							.catch((err) => {
								dispatch({ type: '@base/updateRootProp', payload: { prop: 'platformDeliverySync', value: false } });
							});
					});

					dispatch({ type: '@base/replacePlatform', payload: data });
					setModalConfig({ ...modalConfig });
				}
			})
			.finally(() => {
				// Obtiene los datos en cache
				setLoading(false);
			});
	};

	const handleClickCard = (platform) => {
		dispatch({ type: '@base/updateRootProp', payload: { prop: 'platformSelected', value: platform } });
		history.push({
			pathname: `${props.match.url}/platform/${platform.holeId}`,
			state: platform,
		});
	};

	const handleDownload = (holeid) => {
		setModalConfig({
			...modalConfig,
			cancelProps: { visible: false },
			okProps: { text: 'Aceptar' },
			size: 'large',
			active: true,
			holeid: holeid,
			url: `${documentUrl}${holeid}`,
			onCancel: () => {
				setModalConfig({ ...modalConfig, active: false });
			},
			onSubmit: () => {
				setModalConfig({ ...modalConfig, active: false });
			},
		});
	};

	useDebouncedEffect(
		() => {
			findPlatforms();
		},
		300,
		[]
	);
	return (
		<>
			<Loading active={loading} />
			<Modal href={docRef} {...modalConfig}>
				<div className='pd-14'>
					<PdfViewer url={modalConfig.url} id={modalConfig.holeid} />
				</div>
			</Modal>
			<Switch>
				<Route path={`${props.match.path}/platform/:holeid`} render={(routeProps) => <Details {...routeProps} />}></Route>
				<Route
					path={`${props.match.path}`}
					render={(routeProps) => (
						<PlatformDeliveryIndex {...routeProps} data={platformDelivery} onClick={handleClickCard} onDownload={handleDownload} />
					)}></Route>
			</Switch>
		</>
	);
}

export default PlatformDelivery;

function PlatformDeliveryIndex({ data, onClick, onDownload }) {
	return (
		<>
			{data && data.length > 0 && <DetailsHeader icon={<DillFilled style={{ transform: 'rotateZ(45deg)' }} />} text='Pozos propuestos' />}
			<div className='flex flex-vgap-14 flex-1 flex-column'>
				{data && data.length > 0 ? (
					data?.map((platform) => {
						// Conteo de firmas
						const signatures = { current: 0, total: 0 };

						platform.platformSignatures.forEach((signature) => {
							var responsibles = signature.responsibles;
							signatures.total += responsibles.length;
							signatures.current += responsibles.filter((responsible) => responsible.signed).length;
						});
						return (
							<HoleCard
								key={platform.holeId}
								state={platform.state}
								documentName={platform.documentName}
								currentSignatures={signatures.current}
								totalSignature={signatures.total}
								holeid={platform.holeId}
								onClick={() => onClick && onClick(platform)}
								onClickDocument={onDownload}
							/>
						);
					})
				) : (
					<div className='flex flex-middle flex-1'>
						<NotFound
							icon={(props) => <Parachute {...props} />}
							primaryText='Descarga de datos'
							secondaryText='Debido a las limitaciones de acceso a internet, debe descargar los datos de los sondajes antes de dirigirse a una zona sin cobertura.'
							buttonProps={{ text: 'Descargar datos' }}
							onClick={onDownload}
						/>
					</div>
				)}
			</div>
		</>
	);
}

//
function HoleCard({ onClick, holeid, currentSignatures = 0, totalSignature = 2, documentName, onClickDocument }) {
	const color = currentSignatures < totalSignature ? colors.red : colors.skyblue;
	return (
		<Section onClick={onClick} style={{ fontSize: '1rem', cursor: 'pointer' }} className='platform-card translation-y-hover'>
			<SectionHeader
				left={
					<div style={{}}>
						<div style={{ display: 'inline-block' }}>
							<ProbingTone style={{ fontSize: 26, verticalAlign: 'middle' }} />
						</div>
						<span style={{ marginLeft: 10 }}>{holeid}</span>
					</div>
				}
			/>
			<SectionHeader
				left={
					<div>
						<span style={{ color: color }}>
							<span>{currentSignatures}</span>/<span>{totalSignature}</span> necesarias
						</span>
						<Dot />

						<DescriptionText
							as='span'
							onClick={(e) => {
								documentName !== 'Sin documento' && onClickDocument && onClickDocument(holeid);
								e.stopPropagation();
							}}
							style={{ opacity: documentName !== 'Sin documento' ? 1 : 0.5 }}>
							{documentName}
						</DescriptionText>
					</div>
				}
				right={
					<Tag colorTranslucence={color} style={{ color }}>
						{currentSignatures < totalSignature ? 'Faltan firmas' : 'Autorizada'}
					</Tag>
				}></SectionHeader>
		</Section>
	);
}
