import Loading from '@outlier-spa/loading';
import { DetailsOutlined, ImageOutlined, PdfTone, TrashcanFilled } from '@outlier-spa/mine-icon';
import Button from '@outlier-spa/mine/dist/components/Button';
import DatePicker from '@outlier-spa/mine/dist/components/DatePicker';
import Input, { InputNumber, TextArea } from '@outlier-spa/mine/dist/components/Input';
import Modal from '@outlier-spa/mine/dist/components/Modal';
import { Section, SectionHeader } from '@outlier-spa/mine/dist/components/Section';
import Select from '@outlier-spa/mine/dist/components/Select';
import SwitchOptions from '@outlier-spa/mine/dist/components/SwitchOptions';
import Upload from '@outlier-spa/mine/dist/components/Upload';
import { AddRequest } from 'actions';
import Signature from 'assets/Signature';
import { TitleInput } from 'components/Common';
import DrawSignature, { defaultProps as defaultDrawSignatureProps } from 'components/DrawSignature';
import SignatureViewer from 'components/DrawSignature/SignatureViewer';
import Error from 'components/Error';
import { DetailsHeader } from 'components/Headers';
import PdfViewer from 'components/PdfViewer';
import useDebouncedEffect from 'hooks/useDebouncedEffect';
import moment from 'moment';
import React, { useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { INITIAL_STATE } from 'reducer/requestFormReducer';
import { DescriptionText } from 'styled';
import styled from 'styled-components';
import { resizeImage } from 'utils';
import { db } from 'utils/database';
import 'utils/extensions';
import { REQUEST_STATE } from './utils';

const dateFormat = 'DD/MM/YYYY';
const imageUrl = `${process.env.REACT_APP_API_URL}RequestArea/getImage/`;
const documentUrl = `${process.env.REACT_APP_API_URL}RequestArea/getDocument/`;

const TagSelectStyledOverride = styled.div`
	.rounded-tag-large {
		height: 40px !important;
	}
`;

const toBase64 = (file) =>
	new Promise((resolve, reject) => {
		const reader = new FileReader();
		reader.readAsDataURL(file);
		reader.onload = () => resolve(reader.result);
		reader.onerror = (error) => reject(error);
	});

const RequestForm = React.forwardRef(({ mode = 'edit', ...rest }, ref) => {
	const modalRef = useRef();
	const signatureRef = useRef();
	const modalImageRef = useRef();
	const modalPdfRef = useRef();
	const loadingRef = useRef();
	const [loading, setLoading] = useState(false);
	const [areaOptions, setAreaOptions] = useState(null);
	const [sectorOptions, setSectorOptions] = useState(null);
	const [imagePreview, setImagePreview] = useState(null);
	const state = useSelector((selector) => selector.requestFormReducer);
	const dispatch = useDispatch();

	const handleOpenSignatureModal = (areaIndex, responsibleIndex, prop) => {
		dispatch({ type: '@requestForm/setModalConfig', payload: { title: 'Firma', onSubmit: () => onSubmitModal(areaIndex, responsibleIndex, prop) } });
		modalRef.current.open();
	};

	const onSubmitModal = (areaIndex, responsibleIndex, prop) => {
		signatureRef.current.getBase64Data(function (value) {
			dispatch({ type: '@requestForm/updateSignatureProps', payload: { areaIndex, responsibleIndex, prop, value } });
		});

		dispatch({ type: '@requestForm/updateSignatureProps', payload: { areaIndex, responsibleIndex, prop: 'signed', value: true } });
		modalRef.current.close();
	};

	const updateLogisticsArea = (value) => {
		dispatch({ type: '@requestForm/updateLogisticsArea', payload: value });
	};

	const updateRootProps = (prop, value) => {
		dispatch({ type: '@requestForm/updateRootProp', payload: { prop, value } });
	};

	const updateDateAuthorization = (prop, value) => {
		dispatch({ type: '@requestForm/updateDateAuthorization', payload: { prop, value } });

		//* Si la fecha inicial es menor a la fecha de trmino cambia la fecha de trmino
		if (moment(state.dateAuthorization.from, dateFormat) > moment(state.dateAuthorization.to, dateFormat)) {
			dispatch({
				type: '@requestForm/updateDateAuthorization',
				payload: { prop: 'to', value: moment(state.dateAuthorization.from, dateFormat).add(1, 'days').format(dateFormat) },
			});
		}
	};

	const updateSignatureProps = (areaIndex, responsibleIndex, prop, value) => {
		dispatch({ type: '@requestForm/updateSignatureProps', payload: { areaIndex, responsibleIndex, prop, value } });
	};

	// Eventos de botones de firmas
	const handleAddSignature = (areaIndex) => {
		dispatch({ type: '@requestForm/addSignature', payload: { areaIndex, position: state.signatures[areaIndex].responsibles.length } });
	};

	const handleRemoveSignature = (areaIndex, responsibleIndex) => {
		dispatch({ type: '@requestForm/removeSignature', payload: { areaIndex, responsibleIndex } });
	};

	const handleChangeUpload = async (evt) => {
		const { file } = evt.target.files[0].blobFileStruct();

		resizeImage(file, { w: 640, h: 480 }, 'dataURL', function (uri) {
			setImagePreview(uri);
		});

		dispatch({ type: '@requestForm/updateRootProp', payload: { prop: 'file', value: file } });
	};

	const handleViewImage = () => {
		modalImageRef.current.open();
	};

	const findAreaRequest = (arearequestid) => {
		db.get(`RequestArea/getById?arearequestid=${arearequestid}`).then((response) => {
			if (response.data) {
				dispatch({ type: '@requestForm/updateAllData', payload: response.data });
				AddRequest(response.data).then((resp) => {
					dispatch(resp);
				});
			}
		});
	};

	const findSectors = (value = '') => {
		db.get(`Sector/getAll?sector=${value}`)
			.then((response) => {
				if (response.data && response.data.length > 0) {
					var sectors = response.data.map((sector) => ({ label: sector, value: sector }));
					setSectorOptions(Object.values(sectors));
				}
			})
			.catch((error) => console.debug(error));
	};

	const handleViewPdf = () => {
		modalPdfRef.current.open();
	};

	useEffect(() => {
		if (state.areas?.length > 0) {
			if (state.signatures?.length !== state.areas.length) {
				dispatch({ type: '@requestForm/updateLogisticsArea', payload: state.areas });
			}
		}
	}, [state.areas, state.signatures]);

	useEffect(() => {
		db.get('Area')
			.then((response) => {
				if (response.data && response.data.length > 0) {
					var areasOptions = [];
					response.data.forEach((area) => {
						let areaValues = Object.values(area)[0];
						let areaKeys = Object.keys(area)[0];
						areasOptions.push({ value: areaValues, label: areaValues, key: areaKeys });
					});
					setAreaOptions(areasOptions);
				}
			})
			.catch((error) => console.debug(error));
	}, []);

	useEffect(() => {
		if (mode !== 'edit') {
			dispatch({ type: '@requestForm/updateAllData', payload: INITIAL_STATE });
			updateRootProps('requestState', REQUEST_STATE.CREATED);
		} else {
			const { id } = rest.match.params;
			findAreaRequest(id);
		}
		findSectors();
	}, []);

	// *Validaciones
	const handleCheck = () => {
		var errors = {};
		(!state.areas || state.areas.length === 0) && (errors['areas'] = 'Requerido');
		(!state.description || state.description.trim() === '') && (errors['description'] = 'Requerido');
		(!state.sector || state.sector.length === 0) && (errors['sector'] = 'Requerido');
		!state.file && mode !== 'edit' && (errors['file'] = 'Requerido');
		let wells = [];
		state.recommendedWells &&
			state.recommendedWells.forEach((rw, k) => {
				rw.recommendation.trim() === '' && (errors[`well-${k}`] = 'Requerido');
				wells.includes(rw.recommendation.trim()) && (errors[`well-${k}`] = 'Pozo repetido.');
				wells.push(rw.recommendation.trim());
			});
		state.activities &&
			state.activities.forEach((a, k) => {
				!a.activityName && (errors[`activity-${k}`] = 'Requerido');
				!a.responsible && (errors[`activity-responsible-${k}`] = 'Requerido');
			});

		if (mode === 'edit') {
			!state.signatures && state.signatures.forEach((signature) => {});
		}

		dispatch({ type: '@requestForm/updateRootProp', payload: { prop: 'errors', value: errors } });

		const containError = !!Object.keys(errors).length;
		return { data: state, containError: containError, errors };
	};

	ref && (ref.current = { state: state, getState: handleCheck });

	return (
		<>
			{/* <Loading ref={loadingRef} active={loading}></Loading> */}
			<Modal ref={modalRef} size='large' title='Autorizacin y Firma - Planificacin corto plazo' {...state.modalConfig}>
				<div className='pd-14 '>
					<div className='signature-draw-container'>
						<DrawSignature
							ref={signatureRef}
							{...defaultDrawSignatureProps}
							identifier={state.modalConfig?.identifier}
							canvasWidth='100%'
							canvasHeight='80vh'
						/>
					</div>
					<DescriptionText style={{ marginTop: 20 }}>
						La firma realizada en este recuadro, ser utilizada nicamente en este documento, y no ser guardada o manipulada fuera de este.
					</DescriptionText>
				</div>
			</Modal>
			<Modal ref={modalImageRef} size='large' cancelProps={{ text: 'Cerrar' }} okProps={{ visible: false }}>
				<div className='pd-14'>
					<img src={`${imageUrl}${state.requestId}`} style={{ height: '100%', width: '100%' }} alt='' />
				</div>
			</Modal>

			{state.requestId && mode === 'edit' && (
				<Modal ref={modalPdfRef} cancelProps={{ text: 'Cerrar' }} size='large' okProps={{ visible: false }}>
					{/* <div className='pd-14' style={{ height: '80vh', width: '100%' }}> */}
					<PdfViewer url={`${documentUrl}${state.requestId}`} id={state.requestId} />
					{/* </div> */}
				</Modal>
			)}

			<>
				{mode === 'edit' && (
					<Button prefix={<PdfTone />} onClick={handleViewPdf} style={{ width: 150 }}>
						Ver Solicitud
					</Button>
				)}
				<div className='request-container'>
					<DetailsHeader icon={<DetailsOutlined />} text='Detalles' />
					<div className='flex-vgap-20'>
						<div className='flex flex-gap-14'>
							<div>
								<TitleInput>Fecha inicial</TitleInput>
								<DatePicker
									disabled={mode === 'edit'}
									size='large'
									disabledDate={(current) => current && current <= moment().subtract(1, 'days').endOf('day')}
									value={state.dateAuthorization?.from ? moment(state.dateAuthorization?.from, dateFormat) : moment(dateFormat)}
									placeholder={dateFormat}
									format={dateFormat}
									onChange={(value) => value && updateDateAuthorization('from', value.format(dateFormat))}
								/>
							</div>
							<div>
								<TitleInput>Fecha final</TitleInput>
								<DatePicker
									disabled={mode === 'edit'}
									size='large'
									disabledDate={(current) => current && current <= moment(state.dateAuthorization?.from, dateFormat).endOf('day')}
									value={moment(state.dateAuthorization?.to, dateFormat) || moment(dateFormat)}
									placeholder={dateFormat}
									format={dateFormat}
									onChange={(value) => value && updateDateAuthorization('to', value.format(dateFormat))}
								/>
							</div>
						</div>
						<TagSelectStyledOverride>
							<TitleInput>reas logsticas.</TitleInput>
							<Select
								isError={state.errors && state.errors['areas']}
								message='Requerido'
								disabled={mode === 'edit'}
								mode='tags'
								size='large'
								maxTagCount={0}
								value={state.areas}
								options={areaOptions || []}
								fieldStyle={{ width: 374 }}
								onChange={(value) => updateLogisticsArea(value)}></Select>
						</TagSelectStyledOverride>

						<div className='tags-non-resize'>
							<TitleInput>rea fsica (a solicitar)</TitleInput>
							<Select
								isError={state.errors && state.errors['sector']}
								message='Requerido'
								mode='tags'
								onSearch={() => {}}
								maxTagCount={0}
								disabled={mode === 'edit'}
								fieldStyle={{ width: 374 }}
								size='large'
								options={sectorOptions}
								value={state.sector !== undefined ? [state.sector] : undefined}
								placeholder='Escriba rea a solicitar'
								onChange={(value) => {
									state.errors && value.length > 0 && delete state.errors['sector'];
									updateRootProps('sector', value.length > 0 ? value[value.length - 1] : undefined);
								}}
							/>
						</div>

						<div>
							<TitleInput>Requiere R-GO?</TitleInput>
							<SwitchOptions
								disabled={mode === 'edit'}
								index={state.requiredRgo ? 0 : 1}
								size='large'
								onChange={(_, index) => updateRootProps('requiredRgo', index === 0)}></SwitchOptions>
						</div>

						<div>
							<TitleInput>Descripcin</TitleInput>
							<TextArea
								isError={state.errors && state.errors['description']}
								message='Requerido'
								value={state.description}
								disabled={mode === 'edit'}
								size='large'
								placeholder='Escriba una descripcin para la solicitud'
								style={{ width: 720 }}
								onChange={(value) => {
									state.errors && delete state.errors['description'];
									updateRootProps('description', value);
								}}
							/>
						</div>

						<div>
							<TitleInput>Notas</TitleInput>
							<TextArea
								disabled={mode === 'edit'}
								size='large'
								placeholder='Escriba una nota (Opcional)'
								style={{ width: 720 }}
								onChange={(value) => updateRootProps('requestNote', value)}
							/>
						</div>

						<div>
							<TitleInput>Fotografa o Plano de rea solicitada</TitleInput>
							{mode !== 'edit' && (
								<Error isError={state.errors && state.errors['file']} message='Requerido'>
									{!state.file && (
										<Upload
											disabled={mode === 'edit'}
											multiple
											accept='.png, .jpg, .jpge'
											icon={<ImageOutlined style={{ fontSize: 40 }} />}
											onChange={(value) => {
												state.errors && delete state.errors['file'];
												handleChangeUpload(value);
											}}
										/>
									)}
									{state.file && (
										<Section className='flex flex-middle' style={{ height: 380, position: 'relative' }}>
											<TrashcanFilled
												style={{ position: 'absolute', right: 14, top: 14, cursor: 'pointer' }}
												onClick={() => dispatch({ type: '@requestForm/updateRootProp', payload: { prop: 'file', value: null } })}></TrashcanFilled>
											<img src={imagePreview} style={{ height: 380 }} alt=''></img>
										</Section>
									)}
								</Error>
							)}
							{mode !== 'new' && (
								<Section className='image-preview' style={{ height: 380 }}>
									<div className='flex flex-middle' style={{ height: '100%', cursor: 'pointer' }} onClick={handleViewImage}>
										{/* {`${imageUrl}${state.requestId}`} */}
										<img src={`${imageUrl}${state.requestId}`} loading='lazy' style={{ height: '100%' }} alt='' />
									</div>
								</Section>
							)}
						</div>
						{/* propuestos */}
						<WellSection mode={mode} state={state} dispatch={dispatch} setLoading={setLoading} />

						{/* actividades */}
						<ActivitySection mode={mode} areaOptions={areaOptions} state={state} dispatch={dispatch} />

						<div>
							<>
								<DetailsHeader icon={<Signature />} text='Firmas Responsables' />
								{state.signatures?.map((signature, areaindex) => {
									return (
										<Section key={signature.area} style={{ marginBottom: 20 }}>
											<SectionHeader left={signature.area}></SectionHeader>
											<div className='pd-14'>
												{signature?.responsibles?.map((ma, responsibleIndex) => {
													return (
														<Section key={responsibleIndex} type='inner' style={{ marginBottom: 14 }}>
															<SectionHeader
																left={`Firma ${responsibleIndex + 1}`}
																right={
																	signature?.responsibles.length > 1 &&
																	mode !== 'edit' && (
																		<div className='section-trashcan-container' onClick={() => handleRemoveSignature(areaindex, responsibleIndex)}>
																			<TrashcanFilled />
																		</div>
																	)
																}></SectionHeader>
															<div className='pd-14 signature-section'>
																<TitleInput>Nombres</TitleInput>
																<Input
																	disabled={mode === 'edit'}
																	size='large'
																	value={ma?.personaName}
																	style={{ maxWidth: 560, marginBottom: 20 }}
																	placeholder='Escriba nombres y apellidos'
																	onChange={(value) => updateSignatureProps(areaindex, responsibleIndex, 'personaName', value)}
																/>
																{mode !== 'new' && (
																	<>
																		<TitleInput>Firma</TitleInput>
																		<SignatureViewer
																			disabled={ma.signed}
																			signed={ma.signed}
																			onClick={() => handleOpenSignatureModal(areaindex, responsibleIndex, 'signature')}
																		/>
																	</>
																)}
															</div>
														</Section>
													);
												})}

												{mode === 'new' && (
													<Button type='green' size='large' onClick={() => handleAddSignature(areaindex)}>
														Aadir integrante
													</Button>
												)}
											</div>
										</Section>
									);
								})}
							</>
						</div>
					</div>
				</div>
			</>
		</>
	);
});

export default RequestForm;

function updateRecommendedWell(prop, value, index) {
	return {
		type: '@requestForm/updateRecommendedWells',
		payload: {
			prop: prop,
			value: value,
			index: index,
		},
	};
}

function WellSection({ mode, state, dispatch, setLoading }) {
	const offline = useSelector((selector) => selector.baseReducer.offline);
	const [holeOptions, setHoleOptions] = useState(null);
	const [search, setSearch] = useState('');

	// Actualiza los valores de los pozos propuesto
	const updateRecommendedWells = (prop, value, index, item) => {
		const findHoleFromOptions = holeOptions.find((hole) => hole.value === value);
		dispatch(updateRecommendedWell(prop, value, index));
		dispatch(updateRecommendedWell('east', findHoleFromOptions.Este_Propuesto, index));
		dispatch(updateRecommendedWell('north', findHoleFromOptions.Norte_Propuesto, index));
		dispatch(updateRecommendedWell('elevation', findHoleFromOptions.Elev_Propuesto, index));
		dispatch(updateRecommendedWell('campana', findHoleFromOptions.campana, index));
	};

	const handleRemoveWell = (index) => {
		dispatch({ type: '@requestForm/removeWell', payload: { index } });
	};

	const handleAddWell = () => {
		dispatch({
			type: '@requestForm/setWell',
			payload: { recommendation: '', north: '', east: '', elevation: '' },
		});
	};

	const findHoles = (value = '') => {
		setLoading(true);
		db.get(`HoleLocation/getProposedWell?holeid=${value}`)
			.then((response) => {
				if (response.data && response.data.length > 0) {
					var holes = response.data.map((hole) => ({ ...hole, label: hole.Nombre_Propuesto, value: hole.Nombre_Propuesto }));

					setHoleOptions([...holes]);
				}
			})
			.catch((err) => console.debug(err))
			.finally(() => {
				setLoading(false);
			});
	};

	useDebouncedEffect(
		() => {
			if (offline) return;

			findHoles(search);
		},
		300,
		[search]
	);

	return (
		<Section>
			<SectionHeader left='Recomendaciones a perforar' />
			<div className='pd-14 txt-left'>
				{state?.recommendedWells?.map((well, k) => {
					return (
						<Section key={k} type='inner' style={{ marginBottom: 20 }}>
							<SectionHeader
								left={`Pozo recomentado ${k + 1}`}
								right={state?.recommendedWells.length > 1 && mode !== 'edit' && <TrashcanFilled onClick={() => handleRemoveWell(k)} />}
							/>
							<div className='pd-14'>
								<div className='inline-fields' style={{ width: 440, marginTop: 10 }}>
									<TitleInput>Pozo propuesto</TitleInput>
									<Select
										disabled={mode === 'edit'}
										isError={state.errors && state.errors[`well-${k}`]}
										message={state?.errors && state?.errors[`well-${k}`]}
										size='large'
										options={holeOptions}
										value={well.recommendation}
										placeholder='Seleccione pozo propuesto'
										onChange={(value, item) => {
											state.errors && delete state.errors[`well-${k}`];
											updateRecommendedWells('recommendation', value, k, item);
										}}
										onSearch={setSearch}></Select>
								</div>
								<div className='inline-fields' style={{ width: 180, marginTop: 10 }}>
									<TitleInput>Campaa</TitleInput>
									<InputNumber value={well?.campana} disabled size='large' onChange={(value) => updateRecommendedWells('campana', value, k)} />
								</div>
								<div className='inline-fields' style={{ width: 180, marginTop: 10 }}>
									<TitleInput>Norte</TitleInput>
									<InputNumber value={well?.north} disabled size='large' onChange={(value) => updateRecommendedWells('north', value, k)} />
								</div>
								<div className='inline-fields' style={{ width: 180, marginTop: 10 }}>
									<TitleInput>Este</TitleInput>
									<InputNumber disabled value={well?.east} size='large' onChange={(value) => updateRecommendedWells('east', value, k)} />
								</div>
								<div className='inline-fields' style={{ width: 180, marginTop: 10 }}>
									<TitleInput>Elevacin</TitleInput>
									<InputNumber disabled value={well?.elevation} size='large' onChange={(value) => updateRecommendedWells('elevation', value, k)} />
								</div>
							</div>
						</Section>
					);
				})}
				{mode !== 'edit' && (
					<Button type='green' size='large' onClick={handleAddWell}>
						Aadir recomendacin
					</Button>
				)}
			</div>
		</Section>
	);
}

function ActivitySection({ mode, areaOptions, state, dispatch }) {
	const [activities, setActivities] = useState(null);

	const handleRemoveActivity = (index) => {
		dispatch({ type: '@requestForm/removeActivity', payload: { index } });
	};

	// Actualiza los valores de las actividades
	const updatePropsActivity = (prop, value, index) => {
		dispatch({
			type: '@requestForm/updateActivity',
			payload: {
				prop,
				value,
				index,
			},
		});
	};

	const handleAddActivity = () => {
		dispatch({
			type: '@requestForm/setActivities',
			payload: {
				activityName: null,
				responsible: null,
				from: moment().format(dateFormat),
				to: moment().format(dateFormat),
				observation: '',
			},
		});
	};

	const handleActivitySearch = (value) => {};

	useEffect(() => {
		var flag = true;
		db.get('Activity')
			.then((response) => {
				if (response.data && response.data.length > 0) {
					var activityOptions = [];
					response.data.forEach((activity) => {
						let activityValue = Object.values(activity)[0];

						activityOptions.push({ value: activityValue, label: activityValue });
					});

					setActivities(activityOptions);
				}
			})
			.catch((error) => console.debug(error));

		return () => (flag = false);
	}, []);

	return (
		<Section>
			<SectionHeader left='Actividades a realizar en rea recibida' />
			<div className='pd-14 txt-left'>
				{state?.activities?.map((activity, k) => {
					return (
						<Section key={k} type='inner' style={{ marginBottom: 20 }}>
							<SectionHeader
								left={`Actividad ${k + 1}`}
								right={state?.activities.length > 1 && mode !== 'edit' && <TrashcanFilled onClick={() => handleRemoveActivity(k)} />}
							/>
							<div className='pd-14'>
								<div className='inline-fields' style={{ width: 440, marginTop: 10 }}>
									<TitleInput>Actividad</TitleInput>
									<Select
										disabledTags
										mode='tags'
										size='large'
										isError={state.errors && state.errors[`activity-${k}`]}
										message='Requerido'
										disabled={mode === 'edit'}
										placeholder='Seleccione una actividad'
										value={activity.activityName ? [activity.activityName] : undefined}
										options={activities}
										onSearch={handleActivitySearch}
										onChange={(value, item) => {
											state.errors && delete state.errors[`activity-${k}`];
											updatePropsActivity('activityName', value.length > 0 ? value[value.length - 1] : undefined, k);
										}}
									/>
								</div>
								<div className='inline-fields' style={{ width: 180, marginTop: 10 }}>
									<TitleInput>Responsable</TitleInput>
									<Select
										size='large'
										isError={state.errors && state.errors[`activity-responsible-${k}`]}
										message='Requerido'
										disabled={mode === 'edit'}
										placeholder='Seleccione un responsable'
										options={areaOptions || []}
										value={activity.responsible ?? null}
										fieldStyle={{ width: 180 }}
										onChange={(value, item) => {
											state.errors && delete state.errors[`activity-responsible-${k}`];
											updatePropsActivity('responsible', item.label, k);
										}}
									/>
								</div>
								<div className='inline-fields' style={{ width: 180, marginTop: 10 }}>
									<TitleInput>Fecha inicial</TitleInput>
									<DatePicker
										disabled={mode === 'edit'}
										size='large'
										disabledDate={(current) => {
											const tooLate =
												moment(activity.from, dateFormat) &&
												current < moment(state.dateAuthorization.from, dateFormat).subtract(1, 'days').endOf('day');
											const tooEarly = moment(activity.to, dateFormat) && current > moment(state.dateAuthorization.to, dateFormat);

											return tooLate || tooEarly;
										}}
										value={moment(activity.from, dateFormat)}
										placeholder={dateFormat}
										format={dateFormat}
										onChange={(value) => value && updatePropsActivity('from', value.format(dateFormat), k)}
									/>
								</div>
								<div className='inline-fields' style={{ width: 180, marginTop: 10 }}>
									<TitleInput>Fecha final</TitleInput>
									<DatePicker
										disabled={mode === 'edit'}
										size='large'
										disabledDate={(current) => {
											const tooLate =
												moment(activity.from, dateFormat) &&
												current < moment(state.dateAuthorization.from, dateFormat).subtract(1, 'days').endOf('day');
											const tooEarly = moment(activity.to, dateFormat) && current > moment(state.dateAuthorization.to, dateFormat);
											return tooLate || tooEarly;
										}}
										value={activity.to ? moment(activity.to, dateFormat) : null}
										placeholder={dateFormat}
										format={dateFormat}
										onChange={(value) => updatePropsActivity('to', value ? value.format(dateFormat) : null, k)}
									/>
								</div>
								<div className='inline-fields' style={{ width: 348, marginTop: 10 }}>
									<TitleInput>Observacin </TitleInput>
									<Input
										disabled={mode === 'edit'}
										size='large'
										value={activity.observation}
										placeholder='Escriba un comentario'
										onChange={(value) => updatePropsActivity('observation', value, k)}
									/>
								</div>
							</div>
						</Section>
					);
				})}
				{mode !== 'edit' && (
					<Button type='green' size='large' onClick={handleAddActivity}>
						Aadir actividad
					</Button>
				)}
			</div>
		</Section>
	);
}
