import { NoItemsTone } from '@outlier-spa/mine-icon';
import Button from '@outlier-spa/mine/dist/components/Button';
import React, { useContext } from 'react';
import styled, { ThemeContext } from 'styled-components';
import './styles-notfound.scss';

const NotFoundStyle = styled.div`
	.not-found {
		&-secondary-text {
			color: ${({ theme }) => theme.secondaryColor};
		}
	}
`;

function NotFound({ icon: Component, primaryText, secondaryText, buttonProps, onClick, ...rest }) {
	const theme = useContext(ThemeContext);
	const iconDefault = <NoItemsTone type={theme.dark ? 'dark' : 'light'} style={{ width: 80, height: 80 }}></NoItemsTone>;
	return (
		<NotFoundStyle className={`not-found-container`} {...rest}>
			{(Component && <Component style={{ width: 80, height: 80 }} />) || iconDefault}
			<p className='not-found-principal-text'>{primaryText || `No existe contenido para mostrar`}</p>
			<p className='not-found-secondary-text'>{secondaryText || `Para crear un formulario de entrega, presione el botn Nuevo Formulario`}</p>
			{onClick && (
				<Button className='not-found-button-container' type='green' onClick={onClick} {...buttonProps}>
					{buttonProps.text || `Nuevo Formulario`}
				</Button>
			)}
		</NotFoundStyle>
	);
}

export default NotFound;
