export const PlatformValidations = {
	checkRevision: (platform) => {
		let errors = {};
		let controlMeasures = [];
		let failExists = false;

		platform.revisions.forEach((revision) => {
			if (revision.answer === 'No') {
				const find = platform.controlMeasures.find((control) => control.missingFeature === revision.label);
				errors['revision'] = 'Requerido';
				controlMeasures.push({ missingFeature: revision.label, controlMeasure: find?.controlMeasure ?? '', observation: find?.observation ?? '' });
				revision.answer === 'No' && (failExists = true);
			}
		});
		failExists && (errors['failExists'] = 'Existen revisiones que no cumplen.');

		return { data: platform, errors, controlMeasures };
	},
	checkEmpty: (platform) => {
		let errors = {};

		(!platform.recuperation || platform.recuperation.trim() === '') && (errors['recuperation'] = 'Requerido');
		(!platform.dip || platform.dip.trim() === '') && (errors['dip'] = 'Requerido');
		(!platform.azimuth || platform.azimuth.trim() === '') && (errors['azimuth'] = 'Requerido');
		(!platform.depth || platform.depth.trim() === '') && (errors['depth'] = 'Requerido');
		(!platform.sector || platform.sector.trim() === '') && (errors['sector'] = 'Requerido');

		let failExists = false;

		platform.revisions.forEach((revision) => {
			revision.answer === 'No' && (errors['revision'] = 'Requerido');
			revision.answer === 'No' && (failExists = true);
		});

		failExists && (errors['failExists'] = 'Existen revisiones que no cumplen.');
		// Errores en los inputs de medidas de control

		errors['revision'] &&
			platform.controlMeasures.forEach((measure, k) => {
				measure.missingFeature.trim() === '' && (errors[`missingFeature${k}`] = 'Caracteristica faltante requerida');
				measure.controlMeasure.trim() === '' && (errors[`controlMeasure${k}`] = 'Medida de control es requerido');
			});

		platform.platformSignatures &&
			platform.platformSignatures.forEach((sign) => {
				sign.responsibles.forEach((resp, respIndex) => {
					(!resp.personaName || resp.personaName.trim() === '') &&
						(errors[`personaName-${sign.area}-${respIndex}`] = 'Nombre responsable es requerido');
				});
			});

		if (!platform.materialRisk || platform.materialRisk.trim() === '') errors[`materialRisk`] = 'Requerido';

		if (!errors[`missingFeature0`] || !errors[`controlMeasure0`]) delete errors['revision'];

		const errorLength = Object.keys(errors).length;
		let containError = !!errorLength;

		if (errors && errors['failExists'] && errorLength === 1) containError = false;

		return { data: platform, containError: containError, errors };
	},
};
