import React from 'react';
import styled from 'styled-components';

const ErrorContainer = styled.div``;

function Error({ children, isError, message }) {
	return (
		<ErrorContainer className={`wp-field ${isError ? 'error' : ''}`} data-msg={message} style={{ border: isError ? '1px solid #ff6060' : 'none' }}>
			{children}
		</ErrorContainer>
	);
}

export default Error;

export function Warning({ children, isWarning, message }) {
	return (
		<ErrorContainer
			className={`wp-field ${isWarning ? 'warning' : ''}`}
			data-msg={message}
			style={{ border: isWarning ? '1px solid #ffb116' : 'none' }}>
			{children}
		</ErrorContainer>
	);
}
