export const INITIAL_STATE = {
	holeId: null,
	holes: [],
	hepData: {
		finalName: null,
		type: null,
		observation: null,
		holeId: null,
		requiredGeophysics: true,
	},
	modalConfig: {},
};

export function hepReducer(state = INITIAL_STATE, action) {
	switch (action.type) {
		case '@hep/updateModalConfig':
			return { ...state, modalConfig: { ...state.modalConfig, ...action.payload } };
		case '@hep/updateRootProps':
			const { prop: propRoot, value: valueRoot } = action.payload;
			state[propRoot] = valueRoot;
			return { ...state };
		case '@hep/updateHepData':
			state.hepData['error'] = null; // limpiar errores

			const { prop: hepProp, value: hepValue } = action.payload;
			state.hepData[hepProp] = hepValue;
			return { ...state };

		case 'clean':
			return { ...INITIAL_STATE };
		default:
			return state;
	}
}
