import Loading from '@outlier-spa/loading';
import { LocationTone, SearchOutlined } from '@outlier-spa/mine-icon';
import Button from '@outlier-spa/mine/dist/components/Button';
import Input from '@outlier-spa/mine/dist/components/Input';
import { Section, SectionHeader } from '@outlier-spa/mine/dist/components/Section';
import { colors } from '@outlier-spa/mine/dist/config/themes';
import { AddAreaRequests, CleanAreaRequests, GetAreaRequests } from 'actions';
import { Collapse } from 'antd';
import { BaseLayout } from 'App';
import { HeaderPage } from 'components/Headers';
import NotFound from 'components/NotFound';
import Tag from 'components/Tag';
import useDebouncedEffect from 'hooks/useDebouncedEffect';
import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router';
import { DescriptionText, Dot } from 'styled';
import styled from 'styled-components';
import { groupBy } from 'utils';
import { db } from 'utils/database';
import './collapse-animation.scss';
import './styles.scss';

const { Panel } = Collapse;

const WhiteText = styled.span`
	color: ${({ theme }) => theme.fontColor};
`;

const stateDict = Object.freeze({
	1: { text: 'creada', step: 'Solicitud de rea', bgColor: colors.skyblue, fontColor: colors.skyblue, opacity: 1 },
	2: { text: 'aprobada', step: 'Solicitud de rea', bgColor: colors.skyblue, fontColor: colors.skyblue, opacity: 1 },
	3: { text: 'Faltan firmas', step: 'Solicitud de rea', bgColor: colors.red, fontColor: colors.red, opacity: 1 },
	4: { text: 'creada', step: 'Extensin de solicitud', bgColor: colors.skyblue, fontColor: colors.skyblue, opacity: 1 },
	5: { text: 'aprobada', step: 'Extensin de solicitud', bgColor: colors.skyblue, fontColor: colors.skyblue, opacity: 1 },
	6: { text: 'Faltan firmas', step: 'Extensin de solicitud', bgColor: colors.red, fontColor: colors.red, opacity: 1 },
	7: { text: 'creada', step: 'Devolucin de rea', bgColor: colors.skyblue, fontColor: colors.skyblue, opacity: 1 },
	8: { text: 'rea devuelta', step: 'Devolucin de rea', bgColor: '', fontColor: '', opacity: 0.6 },
	9: { text: 'Faltan firmas', step: 'Devolucin de rea', bgColor: colors.red, fontColor: colors.red, opacity: 1 },
});

const dateformat = 'DD/MM/YYYY';
function RequestAreaIndex(props) {
	const offline = useSelector((selector) => selector.baseReducer.offline);
	const state = useSelector((selector) => selector.baseReducer);
	const [loading, setLoading] = useState(false);
	const dispatch = useDispatch();
	const history = useHistory();
	const [activeCollapseKey, setActiveCollapseKey] = useState(null);
	const [filter, setFilter] = useState('');
	const areaRequests = useSelector((selector) => selector.baseReducer.areaRequests);

	const handleNewRequest = () => {
		history.push('/new-request-area');
	};

	const handleViewRequest = (request) => {
		history.push(`/request-area-view/${request.requestId}`);
	};

	// Realiza la peticin de bsqueda de las solicitudes.
	const findAreaRequest = async (value = '') => {
		setLoading(true);
		if (offline) {
			GetAreaRequests()
				.then((resp) => {
					dispatch(resp);

					console.log(resp);
					var actives = [];
					resp.payload.forEach(async (payload) => {
						actives.push(payload.createAt);
						var rq = await AddAreaRequests(payload);
						dispatch(rq);
					});
					setActiveCollapseKey(actives);
				})
				.finally(() => {
					setLoading(false);
				});
			return;
		}
		CleanAreaRequests().then((resp) => {
			dispatch(resp);
		});

		var actives = [];
		db.get(`RequestArea/getByHole?hole=${value}`)
			.then((response) => {
				if (response.data && response.data.length > 0) {
					var grouped = groupBy(response.data, 'requestCreateAt');
					var groupList = Object.keys(grouped).map((key) => {
						actives.push(key);
						return { createAt: key, areaRequest: grouped[key] };
					});

					groupList.reverse().forEach(async (gl) => {
						var resp = await AddAreaRequests(gl);
						dispatch(resp);
					});
				}
			})
			.finally(() => {
				setLoading(false);
			});
		setActiveCollapseKey(actives);
	};

	useDebouncedEffect(
		() => {
			findAreaRequest(filter);
		},
		300,
		[filter]
	);

	return (
		<BaseLayout className='request-page ' showHelp onChangeTheme={props.onChangeTheme} light={props.light}>
			<Loading active={loading}></Loading>
			<HeaderPage style={{ padding: '19px 0' }}>Solicitudes de rea</HeaderPage>
			<div className='flex flex-gap-10'>
				<div className='flex-1'>
					<Input prefix={<SearchOutlined />} size='large' placeholder='Escriba algn parmetro para filtrar...' onChange={setFilter}></Input>
				</div>
				<div>
					<Button type='green' size='large' onClick={handleNewRequest}>
						Nueva Solicitud
					</Button>
				</div>
			</div>

			<div className='request-cards-container' style={{ marginTop: 10 }}>
				{areaRequests && !loading && (
					<Collapse collapsible='header' expandIconPosition='right' activeKey={activeCollapseKey} onChange={setActiveCollapseKey} ghost>
						{areaRequests.map(({ createAt, areaRequest }) => {
							return (
								<Panel header={createAt} key={createAt}>
									{areaRequest?.map((request) => {
										var holesRenderer = request.recommendedWells.map((hole) => {
											return (
												<Tag key={hole.recommendation}>
													<WhiteText>{hole.recommendation}</WhiteText>,
													<DescriptionText as='span' style={{ marginLeft: 4 }}>
														Prioridad: {hole.details['Prioridad_Perforacio']}
													</DescriptionText>
												</Tag>
											);
										});
										return (
											<RequestCard
												key={request.requestId}
												id={request.requestId}
												beginDate={request.dateAuthorization.from}
												endDate={request.dateAuthorization.to}
												state={request.cardState}
												sector={request.sector}
												holes={holesRenderer}
												rgoName={request.rgoName}
												rgoExists={request.rgoExists}
												signatureProps={{ total: request.totalSignature, signed: request.totalSigned }}
												onClick={() => handleViewRequest(request)}
											/>
										);
									})}
								</Panel>
							);
						})}
					</Collapse>
				)}
			</div>
			{state.areaRequests && state.areaRequests.length === 0 && (
				<div className='flex flex-column flex-1 flex-middle'>
					<NotFound secondaryText='Para crear una solicitud de rea presione el botn Nueva Solicitud' />
				</div>
			)}
		</BaseLayout>
	);
}

export default RequestAreaIndex;

function RequestCard({ id, beginDate = dateformat, endDate = dateformat, holes, state, sector, rgoName, rgoExists, signatureProps, onClick }) {
	const { text, step, bgColor, fontColor, opacity } = stateDict[state];

	return (
		<Section className='request-card translation-y-hover' onClick={onClick} style={{ opacity }}>
			<SectionHeader
				className='request-card-header'
				left={
					<div style={{}}>
						<div style={{ display: 'inline-block' }}>
							<LocationTone style={{ fontSize: 26, verticalAlign: 'middle' }} />
						</div>
						<span style={{ marginLeft: 10 }}>{sector}</span>
					</div>
				}
				right={
					<DescriptionText as='span'>
						{beginDate} a {endDate}
					</DescriptionText>
				}></SectionHeader>
			<div className='pd-14 flex flex-gap-10'>{holes}</div>
			<SectionHeader
				className='request-card-footer'
				left={
					<div>
						<DescriptionText as='span'>{step}</DescriptionText>
						<Dot />
						<label style={{ color: fontColor, fontWeight: 'bold' }}>
							{signatureProps.signed}/{signatureProps.total} firmas
						</label>
						<Dot />
						{rgoExists && (
							<LinkRgoStyled>
								<DescriptionText
									as='a'
									href={`${process.env.REACT_APP_API_URL}Rgo/download/${id}`}
									target='_blank'
									rel='noreferred'
									style={{ textDecoration: 'underline' }}
									onClick={(e) => {
										e.stopPropagation();
									}}>
									{rgoName}
								</DescriptionText>
							</LinkRgoStyled>
						)}
						{!rgoExists && (
							<DescriptionText as='span' style={{ opacity: 0.5 }}>
								{rgoName}
							</DescriptionText>
						)}
					</div>
				}
				right={
					<Tag colorTranslucence={bgColor} style={{ textTransform: 'capitalize', color: fontColor }}>
						{text}
					</Tag>
				}></SectionHeader>
		</Section>
	);
}

const LinkRgoStyled = styled(DescriptionText)`
	display: inline-block;
	&:hover {
		font-weight: 600;
	}
`;
