import { REQUEST_STATE } from 'pages/RequestArea/utils';

export const INITIAL_STATE = { modalConfig: {}, loading: true, requestState: REQUEST_STATE.CREATED, tab: 0 };

export function requestReducer(state = INITIAL_STATE, action) {
	switch (action.type) {
		case '@requestBase/updateModalConfig':
			return { ...state, modalConfig: { ...action.payload } };
		case '@requestBase/updateLoading':
			return { ...state, loading: action.payload };
		case '@requestBase/updateRootProp':
			const { prop, value } = action.payload;

			state[prop] = value;
			return { ...state };
		case '@requestBase/updateAllData':
			return { ...state, ...action.payload };
		default:
			return { ...state };
	}
}
