// import { BlobServiceClient } from '@azure/storage-blob';

const application = 'DrillingWorkflow';
const version = 'V1';

export const INITIAL_STATE = Object.freeze({
	status: false,
	user: null,
	hasDatabaseAccess: false,
	isAuthenticated: false,
	message: null,
	role: null,
	roles: [],
});

export const role = Object.freeze({
	supervisor: 'supervisor',
	laboratoryanalysis: 'laboratoryanalysis',
	support: 'support',
	admin: 'admin',
});

export const permissionTranslate = Object.freeze({
	laboratoryanalysis: 'Laboratorista',
	supervisor: 'Supervisor',
	admin: 'Administrador',
	support: 'Soporte',
});

export const sessionName = `${application}-${version}`;

export const session = sessionStorage.getItem(sessionName);

export const setSessionItem = (value) => sessionStorage.setItem(sessionName, value);

