import { useDispatch } from 'react-redux';
import indexDB from 'utils/db';

const tableName = 'areaRequests';

//Index area requests
export function GetAreaRequests() {
	return indexDB
		.table(tableName)
		.toArray()
		.then((resp) => {
			return { type: '@base/replaceAreaRequests', payload: resp };
		});
}

export function CleanAreaRequests() {
	return indexDB
		.table(tableName)
		.clear()
		.then((resp) => {
			return { type: '@base/cleanAreaRequests' };
		});
}
export function AddAreaRequests(done) {
	return indexDB
		.table(tableName)
		.add(done)
		.then((_) => {
			return {
				type: '@base/addAreaRequests',
				payload: done,
			};
		});
}

// Solicitud de area seleccionada
export function AddRequest(done) {
	return indexDB
		.table('areaRequest')
		.clear()
		.then((_) => {
			return indexDB
				.table('areaRequest')
				.add(done)
				.then((_) => {
					return { type: '@base/addRequest', payload: done };
				});
		});
}
// Cache Platform
export function CachePlatforms(data) {
	return indexDB
		.table('platformCache')
		.toArray()
		.then((platforms) => {
			const filter = platforms.filter((platform) => platform.recuperation === data.recuperation && platform.areaRequestId === data.areaRequestId);
			if (filter.length === 0) {
				return indexDB.table('platformCache').add(data);
			} else {
				var platform = filter[0];
				indexDB.table('platformCache').update(platform.id, data);
			}
		});
}

export function GetCachePlatform(areaRequestId) {
	return indexDB
		.table('platformCache')
		.toArray()
		.then((platforms) => {
			const filter = platforms.filter((platform) => platform.areaRequestId === Number(areaRequestId));
			return { type: '@base/replacePlatform', payload: filter };
		});
}

// HEP
export function CacheHEP(data, areaRequestId) {
	return indexDB
		.table('hepCache')
		.toArray()
		.then((heps) => {
			const filter = heps.filter((hep) => hep.Nombre_Propuesto === data.Nombre_Propuesto && hep.arearequestid === areaRequestId);
			// Si no esta en cache
			if (filter.length === 0) {
				// * Agregar a la base de datos
				return indexDB.table('hepCache').add(data);
			}
			// * Si esta en cache
			else {
				// Actualizar los datos que estn en cach
				var hep = filter[0];
				indexDB.table('hepCache').update(hep.id, data);
			}
		});
}

export function GetCacheHep(areaRequestId) {
	return indexDB
		.table('hepCache')
		.toArray()
		.then((heps) => {
			const filter = heps.filter((hep) => hep.arearequestid === areaRequestId);
			return { type: '@base/replaceHep', payload: filter };
		});
}

export function getCacheHoleDetails(holeId) {
	return indexDB
		.table('hepCache')
		.toArray()
		.then((resp) => {
			var find = resp.find((hole) => hole.holeId === holeId);
			return find ?? {};
		});
}
