import Loading from '@outlier-spa/loading';
import { InfoFilled, LocationTone } from '@outlier-spa/mine-icon';
import Banner from '@outlier-spa/mine/dist/components/Banner';
import Button from '@outlier-spa/mine/dist/components/Button';
import Modal from '@outlier-spa/mine/dist/components/Modal';
import { BaseLayout } from 'App';
import Save from 'assets/Save';
import FixedHeader from 'components/Headers';
import React, { useCallback, useEffect, useReducer, useRef } from 'react';
import { useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { INITIAL_STATE } from 'reducer/requestReturnFormReducer';
import { db } from 'utils/database';
import RequestReturnForm from './RequestReturnForm';
import { REQUEST_STATE } from '../RequestArea/utils';

const initialState = {
	modalConfig: {},
	loading: false,
	sector: '',
	startDate: null,
};

function reducer(state, action) {
	switch (action.type) {
		case 'setModalConfig':
			return state.modalConfig
				? { ...state, modalConfig: { ...state.modalConfig, ...action.payload } }
				: { ...state, modalConfig: { ...action.payload } };

		case 'updateRootProp':
			const { prop, value } = action.payload;

			state[prop] = value;
			return { ...state };

		default:
			return state;
	}
}

function NewReturnRequest(props) {
	const { id: areaRequestId } = props.match.params;
	const returnRef = useRef();
	const loadingRef = useRef();
	const modalRef = useRef();
	const history = useHistory();
	const [state, dispatch] = useReducer(reducer, initialState);
	const globalDispatch = useDispatch();

	const updateRootProps = (prop, value) => {
		dispatch({ type: 'updateRootProp', payload: { prop, value } });
	};

	const handleOpenModal = (value) => {
		modalRef.current.open();
		switch (value) {
			case 'save':
				dispatch({
					type: 'setModalConfig',
					payload: {
						title: 'Crear solicitud de devolucin',
						content: 'Realmente desea crear la solicitud de devolucin.',
						size: 'small',
						okProps: { text: 'Crear' },
						onSubmit: () => handelSave(),
					},
				});
				return;
			case 'cancel':
				dispatch({
					type: 'setModalConfig',
					payload: {
						title: 'Cancelar solicitud de devolucin',
						content: 'Al presionar en "Aceptar" se descartar la solicitud de devolucin',
						size: 'small',
						okProps: { text: 'Aceptar', type: 'danger' },
						onSubmit: () => {
							history.goBack();
						},
					},
				});
				return;
			default:
				return;
		}
	};

	const handelSave = async () => {
		const { data, containError } = returnRef.current.getState();

		modalRef.current.close();
		updateRootProps('loading', true);

		if (containError) {
			updateRootProps('loading', false);
			return;
		}

		data.returnState = REQUEST_STATE.CREATED;

		if (data.withoutSignature) data.returnState = REQUEST_STATE.APPROVED;

		// Guardar solicitud
		db.post(`RequestReturn/saveReturn`, data).then(async (response) => {
			if (response.data) {
				const { state } = response.data;
				if (state === true) {
					history.push(`/request-area-view/${areaRequestId}`);
				}
			}
		});
	};

	const findRequest = useCallback(() => {
		db.get(`RequestArea/getById?arearequestid=${areaRequestId}&showExtension=false&showReturn=false`).then((response) => {
			const { data } = response;
			updateRootProps('requestId', data.requestId);
			updateRootProps('sector', data.sector);
		});
	}, [areaRequestId]);

	useEffect(() => {
		findRequest();
		updateRootProps('returnState', REQUEST_STATE.CREATED);
		globalDispatch({ type: '@returnForm/updateAll', payload: INITIAL_STATE });
	}, [findRequest, globalDispatch]);

	return (
		<>
			<Loading ref={loadingRef} active={state.loading} />
			<Modal ref={modalRef} {...state.modalConfig}>
				<div className='pd-14 '>{state.modalConfig?.content}</div>
			</Modal>
			<BaseLayout cleanInner className='return-area-page'>
				<FixedHeader
					className='sticky top0'
					left={<HeaderLeft name={state.sector} />}
					right={<HeaderRight onCancel={() => handleOpenModal('cancel')} onSubmit={() => handleOpenModal('save')} />}></FixedHeader>
				<Banner>
					<InfoFilled style={{ marginRight: 8, fontSize: '0.875rem' }} />
					Solo puede firmar el documento luego de guardar los cambios con la informacin requerida.
				</Banner>
				<RequestReturnForm ref={returnRef} mode='new' {...props} />
			</BaseLayout>
		</>
	);
}

export default NewReturnRequest;

function HeaderLeft({ name = '' }) {
	return (
		<div className='header-left flex-gap-14' style={{ padding: '21px 0' }}>
			<LocationTone />
			<div>
				<h1>Devolucin de rea</h1>
				<span>{name}</span>
			</div>
		</div>
	);
}

function HeaderRight({ onCancel, onSubmit }) {
	return (
		<div className='header-right flex flex-middle'>
			<Button onClick={onCancel}>Cancelar</Button>
			<Button type='green' prefix={<Save style={{ fontSize: 10 }} />} onClick={onSubmit} style={{ marginLeft: 14 }}>
				Guardar Cambios
			</Button>
		</div>
	);
}
