import { UserAdminAltTone } from '@outlier-spa/mine-icon';
import NavigationMenu, { NavigationMenuItem } from '@outlier-spa/mine/dist/components/NavigationMenu';
import { SwitchTheme } from '@outlier-spa/mine/dist/components/Switch';
import UserContext from 'context/UserContext';
import { permissionTranslate, sessionName } from '../utils';
import _ from 'lodash';
import React, { useContext } from 'react';
import { RoleMenuItem } from 'styled';
import './styles.scss';

export const ProfileNav = (props) => {
	const { auth, setAuth } = useContext(UserContext);
	const { user } = auth;
	var { username, role, roles } = user || {};
	const themeIcon = <SwitchTheme checked={props.light} onChange={({ target }) => props.onChangeTheme(target.checked)} />;
	const content = props.light ? 'Tema claro' : 'Tema oscuro';

	const onChangeRole = (role) => {
		const authAux = _.cloneDeep(auth);
		authAux.user.role = role;
		setAuth({ ...authAux });
		sessionStorage.setItem(sessionName, JSON.stringify(authAux));
	};

	return (
		<div className={`profile-nav`}>
			<NavigationMenu
				title={username}
				subtitle={role && permissionTranslate[role.toLowerCase()] ? permissionTranslate[role.toLowerCase()] : role}
				trigger='click'
				image={<UserAdminAltTone type={true ? 'dark' : 'light'} style={{ width: '100%', height: '100%' }} />}>
				<RoleHeader message='Roles' />
				{roles?.map((r, k) => (
					<RoleMenuItem
						key={k}
						className={`navigation-menu-item-roles ${role.toLowerCase() === r ? 'active' : ''} `}
						content={`${permissionTranslate[r]}`}
						onClick={(_) => onChangeRole(r)}
					/>
				))}

				<NavigationMenuItem prefix={themeIcon} content={content} onClick={(_) => props.onChangeTheme(!props.light)} />
			</NavigationMenu>
		</div>
	);
};

export default ProfileNav;

const RoleHeader = ({ message }) => {
	return (
		<li className='role-menu-item'>
			<div>{message}</div>
			<div className='line'></div>
		</li>
	);
};
