import React from 'react';
import Menu, { MenuItem } from '@outlier-spa/mine/dist/components/Menu';
import themes, { colors } from '@outlier-spa/mine/dist/config/themes';
import { rgba } from 'polished';
import styled, { createGlobalStyle } from 'styled-components';
import { NavigationMenuItem } from '@outlier-spa/mine/dist/components/NavigationMenu';
import Button, { BigIconButton } from '@outlier-spa/mine/dist/components/Button';
import Modal from '@outlier-spa/mine/dist/components/Modal';

export const customTheme = {
	dark: {
		...themes.dark,
	},
	light: {
		...themes.light,
	},
};

export const CustomRootStyles = createGlobalStyle`
	.data-grid-container .data-grid {
		th, td {
			border-color: ${({ theme }) => theme.gridBorderColor};
		}
		.cell{
			background: ${({ theme }) => theme.cellBackground};
			& > input{
				color: ${({ theme }) => theme.fontColor};
			}

			&.read-only,&.header-cell{
				background: ${({ theme }) => theme.cellDisabledBackground};
			}
		}
	}

	table.summary-small__table {
		tr {
			th,
			td {
				&:not(:first-child):not(:only-child) {
					border-left: 1px solid ${({ theme }) => theme.fieldBorder};
				}
			}
			th {

				border-bottom: 1px solid ${({ theme }) => theme.fieldBorder};
			}
		}
	}

    .chart-item {
        .chart-header {
            border-bottom: 1px solid ${({ theme }) => theme.fieldBorder};
        }
    }
    .table-tabs {
        border-bottom: 1px solid ${({ theme }) => theme.fieldBorder};
    }

${'' /* Table antd and pagination table */}
    .ant-pagination-next button,
    .ant-pagination-prev button,
    .ant-pagination-item{
        color: ${({ theme }) => theme.secondaryColor};
    }

    .ant-table {
        color: ${({ theme }) => theme.fontColor};
        background: ${({ theme }) => (theme.dark ? colors.gray3 : colors.white)};
    }

    .ant-table-thead {
        > tr {
            > th {
                color: ${({ theme }) => theme.fontColor};
                border-bottom: 1px solid ${({ theme }) => theme.fieldBorder};
                background-color: ${({ theme }) => (theme.dark ? colors.gray3 : colors.white)};
            }
        }
    }

    .ant-table-thead th.ant-table-column-has-sorters:hover {
        background-color: ${({ theme }) => (theme.dark ? colors.gray2 : colors.softgray1)} ;
    }

    .ant-table-footer {
        background: none;
        .total {
                color: ${({ theme }) => theme.fontColor};
                border-right: 1px solid ${({ theme }) => theme.fieldBorder};
            }
    }
    .ant-table-tbody {
        > tr {
            > td {
                background-color: ${({ theme }) => (theme.dark ? colors.gray3 : colors.backgroundgray)};
                border: 1px solid transparent;

                &:not(:first-child):not(:only-child) {
                    border-left: 1px solid ${({ theme }) => theme.fieldBorder};
                }
            }
            &:nth-child(odd) > td {
                background: ${({ theme }) => (theme.dark ? colors.bluishgray : colors.white)}
            }
        }
    }

    .ant-empty-normal{
        color: ${({ theme }) => theme.fontColor};
    }

    .ant-checkbox-indeterminate .ant-checkbox-inner::after {
        background-color: ${(_) => colors.skyblue};
    }
    
    .ant-checkbox-checked .ant-checkbox-inner::after{
        border-color: ${({ theme }) => (theme.dark ? colors.white : colors.skyblue)};
    }

    .rounded-check__field > i{
        ${'' /* box-shadow: 0 0 0px 1px ${({ theme }) => rgba(theme.fieldBorder, 0.5)}; */}
        background: ${({ theme }) => (theme.dark ? 'none' : colors.white)};
    }

    

    .export__section {
        border-top: 1px solid ${({ theme }) => theme.fieldBorder};
    }

	button.button {
		justify-content: center;
	}

	button.button-red-bg {
		background: ${colors.red};
	}

	button.button-green-bg {
		background: ${colors.green};
	}

	.description-text{
		color: ${({ theme }) => theme.secondaryColor};
		&.warning {
			color: #ffb116;
		}
		&.info {
			.icon {
				font-size: 12px;
			}
		}
	
		> svg {
			margin-right: 6px;
		}
	
		> svg,
		> span {
			display: inline-block;
			vertical-align: middle;
		}
	}
`;

export const Header = styled.div`
	background: ${({ theme }) => theme.headerPage};
`;

export const Title = styled.p`
	font-family: Open Sans;
	font-style: normal;
	font-weight: 600;
	font-size: 14px;
	line-height: 19px;
	margin-bottom: 20px;

	> span {
		font-weight: 600;
		color: ${colors.skyblue};
	}
`;

export const StyledModal = styled(Modal)`
	.modal__content {
		padding-left: 14px;
		padding-right: 14px;
		max-height: 550px;
		overflow-y: auto;
	}

	.modal-large {
		max-width: 1200px;
	}
`;

export const BigIconButtonExport = styled(BigIconButton)`
	.button-bigicon .button {
		padding: 0 !important;
		a,
		span {
			width: 100%;
			height: 100%;
		}

		a {
			line-height: 32px;
		}
	}
`;
export const ButtonExport = styled(Button)`
	&.button {
		padding: 0 !important;
		a {
			padding: 0 20px;
		}
	}
`;

export const Dot = styled.i`
	height: 5px;
	width: 5px;
	border-radius: 50%;
	background-color: ${({ theme }) => theme.fieldBorder};
	display: inline-block;
	vertical-align: middle;
	margin: 0 10px;
`;

// Summary details
export const Details = styled.div`
	background-color: ${({ theme }) => (theme.dark ? colors.bluishgray : colors.white)};
	color: ${({ theme }) => theme.secondaryColor};

	> p {
		&:last-child {
			text-align: right;
			color: ${({ theme }) => theme.fontColor};
		}
	}
`;

export const CleanDetails = styled(Details)`
	background-color: ${({ theme }) => (theme.dark ? colors.gray1 : colors.backgroundgray)};
`;

// Menu Item  and Menu Custom

export const MenuCustom = styled(Menu)`
	&:not(.border-less) {
		border-color: ${({ theme }) => theme.sideNavSecondary};
	}
	.ant-menu {
		background-color: ${({ theme }) => theme.sideNavSecondary};
		color: ${colors.white};
	}

	.ant-menu-submenu-title {
		border-color: none;
	}
`;

export const MenuItemCustom = styled(MenuItem)`
	background-color: none;
	color: ${(_) => colors.white};
	border-bottom-color: ${colors.gray3} !important;
	margin: 0px !important;
	&.ant-menu-item-selected {
		background-color: ${colors.gray3} !important;
	}
`;

export const MenuItemSingle = styled(MenuItemCustom)`
	padding-left: 10px !important;
`;

export const RoleMenuItem = (props) => {
	return (
		<div className='role-navigation-menu-item'>
			<NavigationMenuItem {...props} />
		</div>
	);
};

export const HeaderPage = styled.div`
	background-color: ${({ theme }) => theme.header};
`;

// General Container
export const Wrapper = styled.div`
	background: ${({ theme }) => theme.backgroundMain};
	color: ${({ theme }) => theme.main};
`;

