// Separator
export const Separator = styled.div`
	content: '';
	width: 100%;
	height: 1px;
	background: ${({ theme }) => theme.fieldBorder};
`;

export const DescriptionText = ({ as: Component = 'p', children, className, ...rest }) => {
	return (
		<Component className={`description-text ${className}`} {...rest}>
			{children}
		</Component>
	);
};

const BadgeStyled = styled.div`
	background: ${(props) => colors[props.color] || props.color || colors.blue};
	width: 22px;
	height: 22px;
	color: ${colors.white};
	border-radius: 50%;
	font-size: 14px;
	font-family: Open Sans;
	font-style: normal;
	text-align: center;
	position: relative;
	span {
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		margin: auto;
		font-weight: bold;
		font-size: 14px;
		line-height: 19px;
	}
`;

export const Badge = ({ children, ...rest }) => {
	return (
		<BadgeStyled {...rest}>
			<span>{children}</span>
		</BadgeStyled>
	);
};

export const BadgeMenuItem = ({ children, ...rest }) => (
	<Badge style={{ position: 'absolute', right: 15, top: '50%', transform: 'translateY(-50%)' }} {...rest}>
		{children}
	</Badge>
);

//BASE STYLED
export const FooterStyle = styled.div`
	background-color: ${({ theme }) => theme.footer};
`;

export const SideNavStyle = styled.div`
	background: ${({ theme }) => theme.sideNavMain};
	&.sidenav {
		background-color: ${({ theme }) => theme.sideNavMain};
	}

	.content {
		background-color: ${({ theme }) => theme.sideNavSecondary};
	}
`;
