import { DetailsOutlined, PdfTone, TrashcanFilled } from '@outlier-spa/mine-icon';
import Button from '@outlier-spa/mine/dist/components/Button';
import DatePicker from '@outlier-spa/mine/dist/components/DatePicker';
import Input, { TextArea } from '@outlier-spa/mine/dist/components/Input';
import Modal from '@outlier-spa/mine/dist/components/Modal';
import { Section, SectionHeader } from '@outlier-spa/mine/dist/components/Section';
import Signature from 'assets/Signature';
import { TitleInput } from 'components/Common';
import DrawSignature, { defaultProps as defaultDrawSignatureProps } from 'components/DrawSignature';
import SignatureViewer from 'components/DrawSignature/SignatureViewer';
import { DetailsHeader } from 'components/Headers';
import PdfViewer from 'components/PdfViewer';
import moment from 'moment';
import React, { useEffect, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { DescriptionText } from 'styled';
import '../RequestArea/styles.scss';
import { requestExtensionValidation } from './validation';

const dateFormat = 'DD/MM/YYYY';
const documentUrl = `${process.env.REACT_APP_API_URL}RequestExtension/getDocument/`;

const ExtensionRequest = React.forwardRef(({ mode = 'edit', ...rest }, ref) => {
	const { id } = rest.match.params;
	const modalRef = useRef();
	const signatureRef = useRef();
	const modalPdfRef = useRef();
	const state = useSelector((selector) => selector.requestExtensionFormReducer);
	const dispatch = useDispatch();

	const handleViewPdf = () => {
		modalPdfRef.current.open();
		dispatch({
			type: '@extensionForm/setModalConfig',
			payload: { title: 'Firma', okProps: { visible: false } },
		});
	};

	const handleOpenSignatureModal = (areaIndex, responsibleIndex, prop) => {
		dispatch({
			type: '@extensionForm/setModalConfig',
			payload: { title: 'Firma', onSubmit: () => onSubmitModal(areaIndex, responsibleIndex, prop) },
		});
		modalRef.current.open();
	};

	const onSubmitModal = (areaIndex, responsibleIndex, prop) => {
		signatureRef.current.getBase64Data(null, (uri) => {
			dispatch({ type: '@extensionForm/updateSignatureProps', payload: { areaIndex, responsibleIndex, prop, value: uri } });
		});

		dispatch({ type: '@extensionForm/updateSignatureProps', payload: { areaIndex, responsibleIndex, prop: 'signed', value: true } });
		modalRef.current.close();
	};

	const handleAddSignature = (areaIndex) => {
		dispatch({ type: '@extensionForm/addSignature', payload: { areaIndex, position: state.extensionSignatures[areaIndex].responsibles.length } });
	};

	const handleRemoveSignature = (areaIndex, responsibleIndex) => {
		dispatch({ type: '@extensionForm/removeSignature', payload: { areaIndex, responsibleIndex } });
	};

	const updateSignatureProps = (areaIndex, responsibleIndex, prop, value) => {
		dispatch({ type: '@extensionForm/updateSignatureProps', payload: { areaIndex, responsibleIndex, prop, value } });
	};

	const updateRootProps = (prop, value) => {
		dispatch({ type: '@extensionForm/updateRootProp', payload: { prop, value } });
	};

	const updateDateExtensionAuthorization = (prop, value) => {
		dispatch({ type: '@extensionForm/updateDateExtensionAuthorization', payload: { prop, value } });
	};

	// *Validaciones
	const handleCheck = () => {
		var validation = requestExtensionValidation.checkEmpty(state);
		dispatch({ type: '@extensionForm/updateRootProp', payload: { prop: 'errors', value: validation.errors } });
		return validation;
	};

	useEffect(() => {
		mode === 'new' && dispatch({ type: '@extensionForm/clean' });
		updateRootProps('requestId', Number(id));
		updateRootProps('extensionState', 'creada');
		updateDateExtensionAuthorization(
			'from',
			state.dateExtensionAuthorization?.from
				? moment(state.dateExtensionAuthorization.from, dateFormat).format(dateFormat)
				: rest.startDate && moment(rest.startDate, dateFormat).format(dateFormat)
		);
		updateDateExtensionAuthorization(
			'to',
			state.dateExtensionAuthorization?.to
				? moment(state.dateExtensionAuthorization.to, dateFormat).format(dateFormat)
				: rest.startDate && moment(rest.startDate, dateFormat).add(1, 'days').format(dateFormat)
		);
	}, []);

	ref && (ref.current = { getState: handleCheck });

	return (
		<>
			<Modal ref={modalPdfRef} cancelProps={{ text: 'Cerrar' }} size='large' {...state.modalConfig}>
				{state.requestExtensionId && mode === 'edit' && <PdfViewer url={`${documentUrl}${state.requestExtensionId}`}></PdfViewer>}
			</Modal>

			<Modal ref={modalRef} size='large' {...state.modalConfig}>
				<div className='pd-14 '>
					<div className='signature-draw-container'>
						<DrawSignature ref={signatureRef} {...defaultDrawSignatureProps} canvasWidth='100%' canvasHeight='80vh' />
					</div>
					<DescriptionText style={{ marginTop: 20 }}>
						La firma realizada en este recuadro, ser utilizada nicamente en este documento, y no ser guardada o manipulada fuera de este.
					</DescriptionText>
				</div>
			</Modal>

			{mode === 'edit' && (
				<Button prefix={<PdfTone />} onClick={handleViewPdf}>
					Ver Solicitud
				</Button>
			)}
			<DetailsHeader icon={<DetailsOutlined />} text='Detalles'></DetailsHeader>
			<div className='flex-vgap-20'>
				<div>
					<div className='inline-fields'>
						<TitleInput children='Fecha Inicial' />
						<DatePicker
							size='large'
							disabled={mode === 'edit'}
							disabledDate={(current) => current && current <= moment(rest.startDate, dateFormat).subtract(1, 'days').endOf('day')}
							value={
								state.dateExtensionAuthorization?.from
									? moment(state.dateExtensionAuthorization.from, dateFormat)
									: rest.startDate && moment(rest.startDate, dateFormat)
							}
							placeholder={dateFormat}
							format={dateFormat}
							onChange={(value) => value && updateDateExtensionAuthorization('from', value.format(dateFormat))}
							style={{ width: 180 }}
						/>
					</div>
					<div className='inline-fields'>
						<TitleInput children='Fecha Final' />
						<DatePicker
							size='large'
							disabled={mode === 'edit'}
							disabledDate={(current) => current && current <= moment(rest.startDate, dateFormat).endOf('day')}
							value={
								state.dateExtensionAuthorization?.to
									? moment(state.dateExtensionAuthorization.to, dateFormat)
									: rest.startDate && moment(rest.startDate, dateFormat).add(1, 'days')
							}
							placeholder={dateFormat}
							format={dateFormat}
							onChange={(value) => value && updateDateExtensionAuthorization('to', value.format(dateFormat))}
							style={{ width: 180 }}
						/>
					</div>
				</div>
				<div>
					<TitleInput children='Motivo de la extensin' />
					<TextArea
						size='large'
						disabled={mode === 'edit'}
						isError={state.errors && state.errors['reasonExtension']}
						message={state.errors && state.errors['reasonExtension']}
						value={state.reasonExtension}
						placeholder='Escriba un motivo para la extensin'
						style={{ width: 860 }}
						fieldStyle={{ height: 200 }}
						onChange={(value) => {
							state.errors && delete state.errors['reasonExtension'];
							updateRootProps('reasonExtension', value);
						}}
					/>
				</div>
				<div>
					<DetailsHeader icon={<Signature />} text='Firmas Responsables'></DetailsHeader>
					{state.extensionSignatures?.map((signature, areaIndex) => {
						return (
							<Section style={{ marginBottom: 20 }} key={areaIndex}>
								<SectionHeader left={`Firma ${signature.area} extensin`} />
								<div className='pd-14'>
									{signature.responsibles?.map((ma, responsibleIndex) => {
										return (
											<Section key={responsibleIndex} type='inner' style={{ marginBottom: 14 }}>
												<SectionHeader
													left={`Firma ${responsibleIndex + 1}`}
													right={
														signature.responsibles.length > 1 &&
														mode !== 'edit' && (
															<div className='section-trashcan-container' onClick={() => handleRemoveSignature(areaIndex, responsibleIndex)}>
																<TrashcanFilled />
															</div>
														)
													}
												/>
												<div className='pd-14 signature-section'>
													<TitleInput>Nombres</TitleInput>
													<Input
														size='large'
														disabled={mode === 'edit'}
														isError={state.errors && state.errors[`personaName-${signature.area}-${responsibleIndex}`]}
														message={state.errors && state.errors[`personaName-${signature.area}-${responsibleIndex}`]}
														value={ma.personaName}
														style={{ maxWidth: 560, marginBottom: 20 }}
														placeholder='Escriba nombres y apellidos'
														onChange={(value) => {
															state.errors && delete state.errors[`personaName-${signature.area}-${responsibleIndex}`];
															updateSignatureProps(areaIndex, responsibleIndex, 'personaName', value);
														}}
													/>

													<TitleInput>Firma</TitleInput>
													<SignatureViewer
														disabled={ma.signed}
														signed={ma.signed}
														onClick={() => handleOpenSignatureModal(areaIndex, responsibleIndex, 'signature')}
													/>
												</div>
											</Section>
										);
									})}
									{mode === 'new' && (
										<Button type='green' size='large' onClick={() => handleAddSignature(areaIndex)}>
											Aadir integrante
										</Button>
									)}
								</div>
							</Section>
						);
					})}
				</div>
			</div>
		</>
	);
});

export default ExtensionRequest;
