export const INITIAL_STATE = {
	modalConfig: {},
	dateExtensionAuthorization: { from: null, to: null },
	reasonExtension: '',
	extensionSignatures: [
		{ area: 'Solicita', responsibles: [{ personaName: null, signed: false, position: 0, signature: null }] },
		{ area: 'Autoriza', responsibles: [{ personaName: null, signed: false, position: 0, signature: null }] },
	],
};

export function requestExtensionFormReducer(state = INITIAL_STATE, action) {
	switch (action.type) {
		case '@extensionForm/addSignature':
			const { areaIndex: addAreaIndex, position: positionSignature } = action.payload;
			let extensionSignatures = state.extensionSignatures;

			extensionSignatures[addAreaIndex].responsibles = [
				...extensionSignatures[addAreaIndex].responsibles,
				{ personaName: null, signed: false, position: positionSignature, signature: null },
			];
			return { ...state, extensionSignatures };
		case '@extensionForm/removeSignature':
			const { areaIndex: airemove, responsibleIndex: riremove } = action.payload;
			let signs = state.extensionSignatures;
			let responsiblesFilter = signs[airemove].responsibles.filter((_, k) => k !== riremove);

			signs[airemove].responsibles = responsiblesFilter;
			return { ...state, extensionSignatures: signs };

		case '@extensionForm/updateSignatureProps':
			const { areaIndex: areaindexupd, responsibleIndex: responsibleindexupd, prop: propupd, value: valueupd } = action.payload;
			let signArray = state.extensionSignatures;

			signArray[areaindexupd].responsibles[responsibleindexupd][propupd] = valueupd;
			return { ...state, extensionSignatures: signArray };

		case '@extensionForm/setModalConfig':
			return state.modalConfig
				? { ...state, modalConfig: { ...state.modalConfig, ...action.payload } }
				: { ...state, modalConfig: { ...action.payload } };

		case '@extensionForm/updateRootProp':
			const { prop, value } = action.payload;

			state[prop] = value;
			return { ...state };

		case '@extensionForm/updateDateExtensionAuthorization':
			const { prop: propdateauthorization, value: valuedateauthorization } = action.payload;

			let dateExtensionAuthorization = state.dateExtensionAuthorization;
			dateExtensionAuthorization[propdateauthorization] = valuedateauthorization;
			return { ...state, dateExtensionAuthorization };

		case '@extensionForm/updateAll':
			return { ...state, ...action.payload };

		case '@extensionForm/clean':
			return {
				modalConfig: {},
				dateExtensionAuthorization: { from: null, to: null },
				reasonExtension: '',
				extensionSignatures: [
					{ area: 'Solicita', responsibles: [{ personaName: null, signed: false, position: 0, signature: null }] },
					{ area: 'Autoriza', responsibles: [{ personaName: null, signed: false, position: 0, signature: null }] },
				],
			};
		default:
			return state;
	}
}
