import { v4 as uuidv4 } from 'uuid';

File.prototype.blobFileStruct = function () {
	const extensionFile = /(?:\.([^.]+))?$/;
	const oldName = this.name;
	const guidName = uuidv4();
	const renamedFile = new File([this], `${guidName}.${extensionFile.exec(this.name)[1]}`, { type: this.type });
	return { file: renamedFile, oldName, publicPath: `/public/${renamedFile.name}` };
};
