import { SectionHeader } from '@outlier-spa/mine/dist/components/Section';
import { Table } from 'antd';
import React, { useMemo } from 'react';
import { EmptyPlaceholder } from './styled';

export const defaultColumns = {
	Nombre_Propuesto: 'Propuesto',
	Prioridad_Sondaje: 'Prioridad',
	Norte_Propuesto: 'Norte',
	Este_Propuesto: 'Este',
	Elev_Propuesto: 'Cota',
	Prof_Propuesto: 'Largo',
	SR_Propuesto: 'SR',
	Azimut_Propuesto: 'Azimuth',
	Inclin_Propuesto: 'DIP',
	DDH_Propuesto: 'DDH',
	RC_Propuesto: 'RC',
	Objetivo_Diametro: 'Dimetro',
};

const getColumnsDefinition = (data) => {
	var columns = [];

	Object.keys(defaultColumns).forEach((key, idx) => {
		var letterSize = 12;
		var padding = 14;
		var widthContent = 6 * letterSize + padding;
		var widthTitle = defaultColumns[key].length * letterSize + padding;
		columns.push({
			title: defaultColumns[key],
			dataIndex: key,
			key: key,
			width: idx === 0 ? 190 : widthTitle < widthContent ? widthContent : widthTitle,
			fixed: idx === 0 ? 'left' : undefined,
			ellipsis: true,
		});
	});

	return columns;
};

// { state, dispatch }
export function HepDetails({ data }) {
	const columns = useMemo(() => {
		return getColumnsDefinition(data);
	}, []);

	return (
		<div className='header-details-section'>
			<SectionHeader left='Detalles'></SectionHeader>
			<div className='table-hep'>
				<Table
					columns={columns}
					dataSource={data}
					pagination={false}
					locale={{ emptyText: <EmptyPlaceholder>No existe informacin para mostrar</EmptyPlaceholder> }}
					scroll={{ x: '100%' }}
				/>
			</div>
		</div>
	);
}
