import Loading from '@outlier-spa/loading';
import { CalendarSimpleOutlined, CheckOutlined, ExportFilled, LocationFilled, LocationTone, ReportFilled } from '@outlier-spa/mine-icon';
import Button from '@outlier-spa/mine/dist/components/Button';
import Modal from '@outlier-spa/mine/dist/components/Modal';
import Popover from '@outlier-spa/mine/dist/components/Popover';
import { colors } from '@outlier-spa/mine/dist/config/themes';
import { Collapse, Tabs } from 'antd';
import { BaseLayout } from 'App';
import BoxNoSync from 'assets/BoxNoSync';
import BoxSync from 'assets/BoxSync';
import Geotec from 'assets/Geotec';
import Save from 'assets/Save';
import FixedHeader from 'components/Headers';
import NotFound from 'components/NotFound';
import Tag from 'components/Tag';
import _ from 'lodash';
import Hep from 'pages/Hep';
import PlatformDelivery from 'pages/PlatformDelivery';
import Rgo from 'pages/Rgo';
import React, { useCallback, useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { DescriptionText, Dot } from 'styled';
import { db } from 'utils/database';
import indexDB from 'utils/db';
import './collapse.scss';
import RequestExtensionForm from '../RequestExtension/RequestExtensionForm';
import RequestForm from './RequestForm';
import RequestReturnForm from '../RequestReturn/RequestReturnForm';
import { TabStyle } from './styles';
import './styles.scss';
import { MODE, REQUEST_STATE, TAB_INDEX } from './utils';

const syncProp = {
	syncIcon: <BoxSync style={{ marginLeft: 10, fontSize: 20, verticalAlign: 'middle' }} />,
	noSyncIcon: <BoxNoSync style={{ marginLeft: 10, fontSize: 20, verticalAlign: 'middle' }} />,
	syncText: 'Datos descargados.',
	noSyncText: `Antes de ir a una zona sin conexin a internet, \ndescargue los datos de los  sondajes para completar esta seccin.`,
};

const stateDict = Object.freeze({
	1: { text: 'creada', step: 'Solicitud de rea', bgColor: colors.skyblue, fontColor: colors.skyblue, opacity: 1 },
	2: { text: 'aprobada', step: 'Solicitud de rea', bgColor: colors.skyblue, fontColor: colors.skyblue, opacity: 1 },
	3: { text: 'Faltan firmas', step: 'Solicitud de rea', bgColor: colors.red, fontColor: colors.red, opacity: 1 },
	4: { text: 'creada', step: 'Extensin de solicitud', bgColor: colors.skyblue, fontColor: colors.skyblue, opacity: 1 },
	5: { text: 'aprobada', step: 'Extensin de solicitud', bgColor: colors.skyblue, fontColor: colors.skyblue, opacity: 1 },
	6: { text: 'Faltan firmas', step: 'Extensin de solicitud', bgColor: colors.red, fontColor: colors.red, opacity: 1 },
	7: { text: 'creada', step: 'Devolucin de rea', bgColor: colors.skyblue, fontColor: colors.skyblue, opacity: 1 },
	8: { text: 'rea devuelta', step: 'Devolucin de rea', bgColor: '', fontColor: '', opacity: 0.6 },
	9: { text: 'Faltan firmas', step: 'Devolucin de rea', bgColor: colors.red, fontColor: colors.red, opacity: 1 },
});

const apiPath = `${process.env.REACT_APP_API_URL}RequestArea/download/files/`;

const { TabPane } = Tabs;
const { Panel } = Collapse;

function getKeyByValue(object, value) {
	return Object.keys(object).find((key) => object[key] === value);
}

export function HeaderSeparator({ children }) {
	return (
		<>
			<span className='separator'></span>
			<span className='content'>{children}</span>
			<span className='separator'></span>
		</>
	);
}

const updateModal = (config) => ({ type: '@requestBase/updateModalConfig', payload: config });

function RequestBase({ onChangeTheme, light, status = MODE.EDITING, ...rest }) {
	const { id } = rest.match.params;
	const modalRef = useRef();
	const loadingRef = useRef();
	const requestRef = useRef();
	const rgoRef = useRef();
	const requestExtensionRef = useRef();
	const requestReturnRef = useRef();
	const [loading, setLoading] = useState(false);
	const baseState = useSelector((selector) => selector.baseReducer);
	const state = useSelector((selector) => selector.requestReducer);
	const offline = useSelector((selector) => selector.baseReducer.offline);
	const dispatch = useDispatch();
	const history = useHistory();

	const handleOpenModal = (type) => {
		switch (type) {
			case 'saveRequest':
				modalRef.current.open();
				dispatch(
					updateModal({
						title: 'Guardar Solicitud',
						content: 'Realmente desea guardar la solicitud',
						size: 'small',
						onSubmit: () => handleSave(type),
					})
				);
				break;
			case 'approvedRequest':
				modalRef.current.open();
				dispatch(
					updateModal({
						title: 'Aprobar Solicitud',
						content: 'Realmente desea aprobar las solicitud.',
						size: 'small',
						okProps: { text: 'Aprobar' },
						type: 'primary',
						onSubmit: () => handleSave(type),
					})
				);
				break;
			case 'newExtension':
				history.push(`/extension-request/${state.requestId}`);
				break;
			case 'newReturn':
				history.push(`/return-request/${state.requestId}`);
				break;
			case 'download':
				handleDownload();
				break;

			case 'saveExtension':
				modalRef.current.open();
				dispatch(
					updateModal({
						title: 'Guardar Extensin',
						content: 'Realmente desea guardar la solicitud de extensin.',
						size: 'small',
						onSubmit: () => handleSaveExtension(),
					})
				);
				break;
			case 'removeExtension':
				modalRef.current.open();
				dispatch(
					updateModal({
						title: 'Cancelar Extensin',
						content: 'Realmente desea cancelar la solicitud',
						size: 'small',
						onSubmit: () => handleRemoveExtension(),
						okProps: { type: 'danger', text: 'Aceptar' },
					})
				);
				break;
			case 'saveReturn':
				modalRef.current.open();
				dispatch(
					updateModal({
						title: 'Guardar devolucin',
						content: 'Realmente desea guardar la solicitud de devolucin de rea.',
						size: 'small',
						onSubmit: () => handleSaveReturn(),
					})
				);
				break;

			default:
				break;
		}
	};

	const handleDownload = async () => {
		db.get(`RequestArea/getFiles/${state.requestId}`, { responseType: 'arraybuffer' }).then((response) => {
			if (response.data) {
				let blob = new Blob([response.data], { type: 'application/pdf' });
				let link = document.createElement('a');
				link.href = window.URL.createObjectURL(blob);
				link.download = `Solicitud-preview`;
				document.body.appendChild(link);
				link.click();
				document.body.removeChild(link);
			}
		});
	};

	// * Guarda firmas de solicitud de devolucin de rea
	const handleSaveReturn = () => {
		const { data, containError } = requestReturnRef.current.getState();
		const signMeta = { current: 0, total: 0 };

		modalRef.current.close();
		setLoading(true);

		if (containError) {
			setLoading(false);
			return;
		}
		if (signMeta.current === signMeta.total) {
			data.returnState = REQUEST_STATE.APPROVED;
		}

		db.post(`RequestReturn/saveReturn`, data).then(async (response) => {
			if (response.data) {
				const { state } = response.data;
				if (state === true) {
					history.go();
				}
			}
		});
	};

	// * Guarda extensin (Actualiza las firmas)
	const handleSaveExtension = () => {
		const { data, containError } = requestExtensionRef.current.getState();
		const signMeta = { current: 0, total: 0 };
		const formData = new FormData();

		modalRef.current.close();
		setLoading(true);

		if (containError) {
			setLoading(false);
			return;
		}

		data.extensionSignatures.forEach((signature, index) => {
			signMeta['total'] += signature.responsibles.length;
			signMeta['current'] += signature.responsibles.filter((r) => r.signed).length;
		});

		data.extensionState = REQUEST_STATE.CREATED;

		if (signMeta.current === signMeta.total) {
			data.extensionState = REQUEST_STATE.APPROVED;
		}

		db.post('RequestExtension/save', data)
			.then(async (response) => {
				if (response.data) {
					history.push(`/request-area-view/${state.requestId}`);
				}
			})
			.finally(() => {
				setLoading(false);
			});
	};

	// * Elimina la solicitud
	const handleRemoveExtension = () => {};

	// * Guarda la solicitud de rea
	const handleSave = async (type) => {
		var { data, containError } = requestRef.current.getState();
		const signMeta = { current: 0, total: 0 };
		const formData = new FormData();

		modalRef.current.close();
		setLoading(true);

		if (containError) {
			setLoading(false);
			return;
		}

		const dataClone = _.cloneDeep(data);
		dataClone.signatures = dataClone.signatures.map((sing) => {
			sing.responsibles = sing.responsibles.map((res) => {
				delete res.signature;
				return res;
			});

			return sing;
		});

		data.signatures.forEach((signature, index) => {
			signMeta['total'] += signature.responsibles.length;
			signMeta['current'] += signature.responsibles.filter((r) => r.signed).length;
			signature.responsibles.forEach((responsible) => {
				var file = responsible.signature;
				formData.append(`file-${signature.area}-${index}-${responsible.position}-${responsible.personaName}`, file);
			});
		});

		if (signMeta.current === signMeta.total) {
			dataClone.requestState = REQUEST_STATE.APPROVED;
		}

		db.post('RequestArea/SaveRequest', dataClone).then(async (response) => {
			if (response.data) {
				db.post(`RequestArea/updateSignature/${state.requestId}`, formData, {
					processData: false,
					headers: { 'Content-Type': 'multipart/form-data' },
					responseType: 'arraybuffer',
				}).then((responseSignature) => {
					history.go();
				});
			}
		});
	};

	//* Habilita las secciones siguientes (HEP/ entrega de plataforma)
	const handleRgoComplete = () => {
		findAreaRequest(id);
	};

	const findAreaRequest = useCallback(async (arearequestid) => {
		setLoading(true);
		if (offline) {
			setLoading(false);
			return;
		}
		const platformCache = await indexDB.table('platformCache').toArray();
		const hepCache = await indexDB.table('hepCache').toArray();

		db.get(`RequestArea/getById?arearequestid=${arearequestid}`).then((response) => {
			if (response.data) {
				const { data } = response;
				let sync = true;
				let hepSync = true;
				data.recommendedWells.forEach((r) => {
					sync = platformCache.length > 0 ? sync && !!platformCache.find((p) => p.recuperation === r.recommendation) : false;
					hepSync = hepCache.length > 0 ? hepSync && !!hepCache.find((hc) => hc.Nombre_Propuesto === r.recommendation) : false;
				});

				dispatch({
					type: '@requestBase/updateAllData',
					payload: {
						platformDeliverySync: sync,
						hepSync: hepSync,
						state: data.requestState,
						requestId: arearequestid,
						nameRequest: data.sector,
						existsExtension: data.existsExtension,
						existsReturn: data.existsReturn,
						all: data,
						rgoExists: data.rgoExists,
						extensionState: data.extensionState,
						returnState: data.returnState,
						requiredRgo: data.requiredRgo,
						totalSigned: data.totalSigned,
						totalSignature: data.totalSignature,
						cardState: data.cardState,
						from: !data.existsExtension
							? data.dateAuthorization.from
							: data.existsReturn
							? data.returnCreateAt
							: data.existsExtension && !data.existsReturn && (data.extensionState === REQUEST_STATE.APPROVED || REQUEST_STATE.CREATED)
							? data.dateExtensionAuthorization.from
							: '',
						to: !data.existsExtension
							? data.dateAuthorization.to
							: data.existsReturn
							? data.returnCreateAt
							: data.existsExtension && !data.existsReturn && (data.extensionState === REQUEST_STATE.APPROVED || REQUEST_STATE.CREATED)
							? data.dateExtensionAuthorization.to
							: '',
					},
				});

				dispatch({ type: '@base/updateAll', payload: { platformDeliverySync: sync, hepSync: hepSync } });
				// Actualiza el estado global para la solicitud de extension
				dispatch({ type: '@extensionForm/updateAll', payload: data });
				// Actualiza el estado global para la solicitud de devolucin
				dispatch({ type: '@returnForm/updateAll', payload: data });
				setLoading(false);
			}
		});
	}, []);

	useEffect(() => {
		findAreaRequest(id);

		return () => dispatch({ type: '@base/updateRootProp', payload: { prop: 'tab', value: TAB_INDEX.REQUEST } });
	}, [id]);

	const hiddenExtension =
		state.state === REQUEST_STATE.CREATED ||
		state.extensionState === REQUEST_STATE.CREATED ||
		state.returnState === REQUEST_STATE.CREATED ||
		state.returnState === REQUEST_STATE.APPROVED ||
		state.tab === TAB_INDEX.RGO ||
		state.tab === TAB_INDEX.HEP ||
		state.tab === TAB_INDEX.PLATFORM_DELIVERY ||
		(!state.rgoExists && state.requiredRgo);

	const hiddenReturn =
		state.state === REQUEST_STATE.CREATED ||
		(state.existsExtension && state.extensionState === REQUEST_STATE.CREATED) ||
		state.returnState === REQUEST_STATE.CREATED ||
		state.returnState === REQUEST_STATE.APPROVED ||
		state.tab === TAB_INDEX.RGO ||
		state.tab === TAB_INDEX.HEP ||
		state.tab === TAB_INDEX.PLATFORM_DELIVERY ||
		(!state.rgoExists && state.requiredRgo);

	const hepDisabled = state.state === REQUEST_STATE.CREATED || (state.requiredRgo && !state.rgoExists);
	const platformDisabled = state.state === REQUEST_STATE.CREATED || (state.requiredRgo && !state.rgoExists);

	const defaultAccordion =
		state.returnState === REQUEST_STATE.CREATED || state.returnState === REQUEST_STATE.APPROVED
			? 'return'
			: state.extensionState === REQUEST_STATE.CREATED || state.extensionState === REQUEST_STATE.APPROVED
			? 'extension'
			: 'request';

	return (
		<>
			<Loading ref={loadingRef} active={loading} />
			<Modal ref={modalRef} {...state?.modalConfig}>
				<div className='pd-14'>{state?.modalConfig?.content || null}</div>
			</Modal>
			<BaseLayout className='request-page especial-collapse' cleanInner onChangeTheme={onChangeTheme} light={light}>
				{state.state && !loading && (
					<FixedHeader
						className='sticky top0'
						showSeparator={false}
						left={<HeaderLeft {...state} />}
						right={
							<HeaderRight
								showDownload={state.state === REQUEST_STATE.APPROVED}
								showNewExtension={!hiddenExtension}
								showNewReturn={!hiddenReturn}
								showSave={state.state === REQUEST_STATE.CREATED && state.totalSignature !== state.totalSigned}
								showExtensionSave={state.extensionState === REQUEST_STATE.CREATED}
								showRemoveExtension={state.extensionState === REQUEST_STATE.CREATED}
								//Return
								showReturnSave={state.returnState === REQUEST_STATE.CREATED}
								downloadPath={`${apiPath}${state.requestId}`}
								onClick={handleOpenModal}
							/>
						}></FixedHeader>
				)}
				{!state.state && (
					<div className='flex flex-middle flex-1'>
						<NotFound />
					</div>
				)}
				{state.state && !loading && state.state === REQUEST_STATE.CREATED && <RequestForm ref={requestRef} {...rest} />}
				{state.state && !loading && state.state !== REQUEST_STATE.CREATED && (
					<TabStyle
						activeKey={getKeyByValue(TAB_INDEX, baseState.tab)}
						onChange={(value) => {
							history.push(rest.match.url);
							dispatch({ type: '@base/updateRootProp', payload: { prop: 'tab', value: TAB_INDEX[value] } });
						}}>
						<TabPane
							disabled={false}
							tab={
								<HeaderSeparator>
									<ReportFilled />
									<span style={{ marginLeft: 10 }}>Detalle Solicitud</span>
								</HeaderSeparator>
							}
							key='REQUEST'>
							<Collapse accordion defaultActiveKey={[defaultAccordion]}>
								<Panel
									header={
										<HeaderSeparator>
											<LocationFilled />
											<span style={{ marginLeft: 10 }}>Solicitud de rea</span>
										</HeaderSeparator>
									}
									key='request'>
									<RequestForm ref={requestRef} {...rest} />
								</Panel>

								{state.existsExtension && (
									<Panel
										header={
											<HeaderSeparator>
												<CalendarSimpleOutlined />
												<span style={{ marginLeft: 10 }}>Extensin de solicitud</span>
											</HeaderSeparator>
										}
										key='extension'>
										<RequestExtensionForm ref={requestExtensionRef} {...rest} />
									</Panel>
								)}

								{state.existsReturn && (
									<Panel
										header={
											<HeaderSeparator>
												<ExportFilled />
												<span style={{ marginLeft: 10 }}>Devolucion de rea</span>
											</HeaderSeparator>
										}
										key='return'>
										{state?.all && <RequestReturnForm ref={requestReturnRef} {...rest} initial={state?.all} />}
									</Panel>
								)}
							</Collapse>
						</TabPane>
						<TabPane
							disabled={state.state === REQUEST_STATE.CREATED || !state.requiredRgo}
							tab={
								<>
									<Geotec /> <span style={{ marginLeft: 10 }}>R-GO</span>
								</>
							}
							key='RGO'>
							{id && <Rgo ref={rgoRef} requestId={id} sector={state.nameRequest} onComplete={handleRgoComplete} />}
						</TabPane>
						<TabPane
							style={{ height: 'max-content' }}
							disabled={hepDisabled}
							tab={
								<TabTitle
									disabled={hepDisabled}
									sync={baseState.hepSync}
									disabledText='Para habilitar esta seccion debe agregar un RGO'
									title='HEP'
								/>
							}
							key='HEP'>
							<Hep areaRequestId={id} />
						</TabPane>
						<TabPane
							disabled={platformDisabled}
							tab={
								<TabTitle
									disabled={platformDisabled}
									sync={baseState.platformDeliverySync}
									disabledText='Para habilitar esta seccion debe agregar un RGO'
									title='Formulario Entrega'
								/>
							}
							key='PLATFORM_DELIVERY'>
							<PlatformDelivery requestId={id} {...rest} />
						</TabPane>
					</TabStyle>
				)}
			</BaseLayout>
		</>
	);
}

export default RequestBase;

function HeaderLeft({ nameRequest, totalSigned = 0, totalSignature = 0, from, to, cardState }) {
	const { bgColor, fontColor, step } = stateDict[cardState] ?? { bgColor: '', fontColor: '' };

	return (
		<div className='header-left'>
			<LocationTone />
			<div className='header-left-text-container'>
				<div className='header-left-principal-text'>
					<DescriptionText as='span'>{nameRequest}</DescriptionText>
					<Tag size='small' rounded colorTranslucence={bgColor} style={{ color: fontColor }}>
						{totalSignature - totalSigned !== 0 ? 'Faltan Firmas' : 'Firmado'}
					</Tag>
				</div>
				<span className='header-left-secondary-text'>
					<DescriptionText as='span'>{step}</DescriptionText>
					<Dot />
					<span style={{ color: fontColor, fontWeight: 600 }}>
						{totalSigned}/{totalSignature} Firmas necesarias
					</span>
					<Dot />
					<DescriptionText as='span'>
						{from} a {to}
					</DescriptionText>
				</span>
			</div>
		</div>
	);
}

function HeaderRight({
	onRemove,
	showSave,
	onClick,
	showApproved,
	showDownload,
	showNewExtension,
	showExtensionSave,
	showRemoveExtension,
	showReturnSave,
	showNewReturn,
	downloadPath,
}) {
	return (
		<div className='flex flex-gap-10 flex-middle'>
			{showDownload && (
				<>
					<a className='button green' href={downloadPath} target='_blank' rel='noreferrer'>
						<ReportFilled style={{ fontSize: 10, marginRight: 10 }} /> <span style={{ whiteSpace: 'nowrap' }}>Descargar Archivos</span>
					</a>
				</>
			)}
			{onRemove && (
				<Button onClick={onRemove} style={{ marginLeft: 14 }}>
					Cancelar Solicitud
				</Button>
			)}

			{showRemoveExtension && (
				<Button onClick={() => onClick('removeExtension')} style={{ marginLeft: 14 }}>
					<span style={{ whiteSpace: 'nowrap' }}>Cancelar Extensin</span>
				</Button>
			)}
			{showSave && (
				<Button type='green' prefix={<Save style={{ fontSize: 10 }} />} onClick={() => onClick('saveRequest')} style={{ marginLeft: 14 }}>
					<span style={{ whiteSpace: 'nowrap' }}>Guardar Cambios Solicitud</span>
				</Button>
			)}
			{showExtensionSave && (
				<Button type='green' prefix={<Save style={{ fontSize: 10 }} />} onClick={() => onClick('saveExtension')} style={{ marginLeft: 14 }}>
					<span style={{ whiteSpace: 'nowrap' }}>Guardar Cambios Extensin</span>
				</Button>
			)}
			{showReturnSave && (
				<Button type='green' prefix={<Save style={{ fontSize: 10 }} />} onClick={() => onClick('saveReturn')} style={{ marginLeft: 14 }}>
					<span style={{ whiteSpace: 'nowrap' }}>Guardar Cambios Devolucin</span>
				</Button>
			)}

			{showApproved && (
				<Button
					prefix={<CheckOutlined style={{ fontSize: 10 }} />}
					onClick={() => onClick('approvedRequest')}
					type='primary'
					style={{ marginLeft: 14 }}>
					<span style={{ whiteSpace: 'nowrap' }}>Finalizar aprobacin</span>
				</Button>
			)}

			{showNewExtension && (
				<Button prefix={<CalendarSimpleOutlined style={{ fontSize: 10 }} />} onClick={() => onClick('newExtension')} style={{ marginLeft: 14 }}>
					<span style={{ whiteSpace: 'nowrap' }}>Solicitar Extensin</span>
				</Button>
			)}
			{showNewReturn && (
				<Button prefix={<ExportFilled style={{ fontSize: 10 }} />} onClick={() => onClick('newReturn')} style={{ marginLeft: 14 }}>
					<span style={{ whiteSpace: 'nowrap' }}>Devolucin de rea</span>
				</Button>
			)}
		</div>
	);
}

function TabTitle({ disabled, sync, disabledText, title }) {
	return (
		<>
			{!disabled && <span style={{ marginLeft: 10 }}>{title}</span>}
			{disabled && (
				<Popover trigger='hover' content={<span>{disabledText}</span>}>
					{title}
				</Popover>
			)}
			{!disabled && (
				<>
					<Popover trigger='hover' content={<span>{sync ? syncProp.syncText : syncProp.noSyncText}</span>}>
						{sync ? syncProp.syncIcon : syncProp.noSyncIcon}
					</Popover>
				</>
			)}
		</>
	);
}
