import { ReportFilled, TrashcanFilled } from '@outlier-spa/mine-icon';
import Button from '@outlier-spa/mine/dist/components/Button';
import DatePicker from '@outlier-spa/mine/dist/components/DatePicker';
import Input from '@outlier-spa/mine/dist/components/Input';
import Modal from '@outlier-spa/mine/dist/components/Modal';
import Select from '@outlier-spa/mine/dist/components/Select';
import Upload from '@outlier-spa/mine/dist/components/Upload';
import Save from 'assets/Save';
import { TitleInput } from 'components/Common';
import Error from 'components/Error';
import FixedHeader from 'components/Headers';
import moment from 'moment';
import React, { useEffect, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { toBase64 } from 'utils';
import { db } from 'utils/database';
import './styles.scss';
import Loading from '@outlier-spa/loading';
import PdfViewer from 'components/PdfViewer';
import { REQUEST_STATE } from 'pages/RequestArea/utils';

const documentUrl = `${process.env.REACT_APP_API_URL}Rgo/getDocument/`;
const dateFormat = 'DD/MM/YYYY';

const updateProps = (prop, value) => ({ type: '@rgo/updateRootProps', payload: { prop: prop, value: value } });

const addProp = (data) => ({ type: '@rgo/addProps', payload: data });

const activeLoading = (active = true) => ({ type: '@rgo/updateLoading', payload: active });

const updateModal = (config) => ({ type: '@rgo/updateModalConfig', payload: config });

const Rgo = React.forwardRef(({ mode = 'edit', ...rest }, ref) => {
	const modalRef = useRef();
	const loadingRef = useRef();
	const state = useSelector((selector) => selector.rgoReducer);
	const dispatch = useDispatch();
	const history = useHistory();
	const offline = useSelector((selector) => selector.baseReducer.offline);
	const { requestId, sector } = rest;

	const updateRootProps = (prop, value) => {
		dispatch(updateProps(prop, value));
	};

	const handleChangeUpload = async (evt) => {
		const { file } = evt.target.files[0].blobFileStruct();
		dispatch(updateProps('rgoFile', file));
		dispatch(updateProps('filePreview', await toBase64(file)));
	};

	const check = () => {
		var errors = {};
		!state.sector && state.sector.trim() === '' && (errors['sector'] = 'Requerido');

		updateRootProps('errors', errors);
		const containError = !!Object.keys(errors).length;

		return { data: state, containError: containError, errors };
	};

	const handleRemovePreview = () => {
		dispatch(updateProps('filePreview', undefined));
	};

	const handleOpenModal = (type) => {
		switch (type) {
			case 'saveRgo':
				modalRef.current.open();
				dispatch(updateModal({ title: 'Guardar RGO', content: 'Realmente quiere guardar el RGO ', size: 'small', onSubmit: () => handleSave() }));

				break;

			default:
				break;
		}
	};

	const handleSave = async () => {
		const { data, containError } = check();
		dispatch(activeLoading());
		modalRef.current.close();

		if (containError) {
			dispatch(activeLoading(false));
			return;
		}

		const { data: dataSave } = await db.post(`Rgo/saveRgo`, data);
		const { rgoId, state } = dataSave;

		// Guardar documento
		if (state && data.rgoFile) {
			const formData = new FormData();
			formData.append('files[]', data.rgoFile);

			const rgoDocumentResult = await db.post(`Rgo/saveDocument/${rgoId}`, formData, {
				processData: false,
				headers: { 'Content-Type': 'multipart/form-data' },
			});

			if (rgoDocumentResult.status === 200) rest.onComplete && rest.onComplete();
		}

		dispatch(activeLoading(false));
		findRgo();
	};

	// Busca todos los sectores
	const findSectors = (value = '') => {
		if (offline) {
			dispatch(activeLoading(false));
			return;
		}
		db.get(`Sector/getAll?sector=${value}`)
			.then((response) => {
				if (response.data && response.data.length > 0) {
					var sectors = response.data.map((sector) => ({ label: sector, value: sector }));
					updateRootProps('sectors', Object.values(sectors));
					dispatch(activeLoading(false));
				}
			})
			.catch((error) => console.debug(error));
	};

	// Busca los datos de la RGO
	const findRgo = () => {
		db.get(`Rgo/get/${requestId}`).then((response) => {
			if (response.data) {
				const { data } = response;
				data.date = data.date ? moment(data.date).format(dateFormat) : moment();
				dispatch(addProp(data));
			}
			// NO CONTENT
			if (response.status === 204) {
				dispatch(updateProps('date', moment()));
			}
		});
	};

	useEffect(() => {
		findSectors();
		updateRootProps('sector', sector);
		updateRootProps('areaRequestId', requestId);
		findRgo();
	}, []);

	const rgoDisabled = state.state === REQUEST_STATE.CREATED || state.state === REQUEST_STATE.APPROVED;

	ref && (ref.current = { state: state, getState: check });

	return (
		<>
			<Modal ref={modalRef} {...state.modalConfig}>
				<div className='pd-14'>{state.modalConfig?.content}</div>
			</Modal>
			<Loading ref={loadingRef} active={state.loading} />
			<div className='flex flex-column flex-vgap-20 rgo-container'>
				<FixedHeader left={<HeaderLeft state='error' />} right={<HeaderRight showSave={!rgoDisabled} onClick={handleOpenModal} />}></FixedHeader>

				<div>
					<TitleInput>Nombre</TitleInput>
					<Input
						disabled={rgoDisabled}
						size='large'
						placeholder='Escriba nombre rgo'
						value={state.name}
						fieldStyle={{ width: 374 }}
						onChange={(value) => {
							updateRootProps('name', value);
						}}></Input>
				</div>
				<div>
					<TitleInput>Restriccin</TitleInput>
					<Input
						disabled={rgoDisabled}
						size='large'
						placeholder='Escriba restriccin'
						value={state.restriction}
						fieldStyle={{ width: 374 }}
						onChange={(value) => {
							updateRootProps('restriction', value);
						}}
					/>
				</div>
				<div>
					<TitleInput>Sector</TitleInput>
					<Select
						disabled={rgoDisabled}
						size='large'
						placeholder='Seleccione sector'
						value={state?.sector}
						fieldStyle={{ width: 374 }}
						options={state?.sectors}
						onChange={(value, v2) => {
							updateRootProps('sector', value);
						}}
					/>
				</div>
				<div>
					<TitleInput>Fecha</TitleInput>
					{state.date && (
						<DatePicker
							disabled={rgoDisabled}
							size='large'
							placeholder='Seleccione fecha de rgo'
							format={dateFormat}
							value={moment.isMoment(moment(state.date)) ? moment(state.date, dateFormat) : moment()}
							disabledDate={(current) => current < moment().subtract(1, 'days').endOf('day')}
							fieldStyle={{ width: 374 }}
							onChange={(value) => {
								updateRootProps('date', value ? value.format(dateFormat) : null);
							}}
						/>
					)}
				</div>
				<div>
					<div className=' flex flex-space trashcan'>
						<TitleInput> {state.documentExists ? 'Rgo existente' : 'Subir Rgo'}</TitleInput>
						{state.filePreview && <TrashcanFilled onClick={handleRemovePreview} />}
					</div>
					<Error isError={state.errors && state.errors['file']} message='Requerido'>
						{!rgoDisabled && !state.filePreview && (
							<Upload
								multiple
								accept='.pdf, .docx, .doc'
								icon={<ReportFilled style={{ fontSize: 40 }} />}
								onChange={(value) => {
									state.errors && delete state.errors['file'];
									handleChangeUpload(value);
								}}
							/>
						)}
						{state.documentExists && <PdfViewer url={`${documentUrl}${state.rgoId}`} />}
						{state.filePreview && <PdfViewer url={state.filePreview} />}
					</Error>
				</div>
			</div>
		</>
	);
});

export default Rgo;

function HeaderLeft({ state }) {
	return (
		<>
			<p>RGO</p>
			<span className={`${state === 'success' ? 'success' : 'error'}`}></span>
		</>
	);
}

// Acciones del lado derecho de la cabecera
function HeaderRight({ showSave = true, onClick }) {
	return (
		<div className='flex flex-center'>
			{showSave && (
				<Button type='green' prefix={<Save style={{ width: 14, height: 14 }} />} onClick={() => onClick && onClick('saveRgo')}>
					Guardar Cambios
				</Button>
			)}
		</div>
	);
}
