import { Section } from '@outlier-spa/mine/dist/components/Section';
import React from 'react';
import './styles.scss';
import Signature from 'assets/Signature';
import { colors } from '@outlier-spa/mine/dist/config/themes';
import { DescriptionText } from 'styled';
import { CheckOutlined } from '@outlier-spa/mine-icon';

export const SIGNED_TEXT = 'Firmado por el participante';
export const NO_SIGNED_TEXT = 'Seleccione para firmar';
const defaultStyles = { color: colors.skyblue, width: 'auto', height: 'auto' };

function SignatureViewer({ disabled, svg, signed = false, signedText = SIGNED_TEXT, noSignedText = NO_SIGNED_TEXT, onClick }) {
	return (
		<Section className='signature-viewer' style={{ height: 150, maxWidth: 560, pointerEvents: disabled ? 'none' : 'auto' }}>
			<div onClick={onClick}>
				{svg || (
					<label style={{ textAlign: 'center', cursor: 'pointer' }}>
						{!signed && (
							<>
								<Signature style={{ ...defaultStyles, fontSize: 22 }} />
								<DescriptionText as='p' style={{ opacity: 0.5 }}>
									{noSignedText}
								</DescriptionText>
							</>
						)}
						{signed && (
							<>
								<CheckOutlined style={{ ...defaultStyles, verticalAlign: 'middle', marginRight: 9, fontSize: '1rem' }} />
								<DescriptionText as='span' style={{ opacity: 0.5 }}>
									{signedText}
								</DescriptionText>
							</>
						)}
					</label>
				)}
			</div>
		</Section>
	);
}

export default SignatureViewer;
