export const INITIAL_STATE = {
	errors: { revision: 'Requerido' },
	loading: false,
	modalConfig: {},
	recuperation: '',
	dip: '',
	azimuth: '',
	depth: '',
	sector: '',
	revisions: [
		{ key: 1, label: 'Aplitud 30x30', answer: 'No' },
		{ key: 2, label: 'Pretiles Aljibe', answer: 'No' },
		{ key: 3, label: 'Bermas (trnsito peatonal)', answer: 'No' },
		{ key: 4, label: 'Pretiles contorno caseta', answer: 'No' },
		{ key: 5, label: 'Accesos vehculos y personal', answer: 'No' },
		{ key: 6, label: 'Pretiles contorno', answer: 'No' },
		{ key: 7, label: 'Nivelacin plataforma', answer: 'No' },
		{ key: 8, label: 'Estacionamientos', answer: 'No' },
		{ key: 9, label: 'Pretil contorno y tope bao', answer: 'No' },
	],
	controlMeasures: [
		{ missingFeature: 'Aplitud 30x30', controlMeasure: '', observation: '' },
		{ missingFeature: 'Pretiles Aljibe', controlMeasure: '', observation: '' },
		{ missingFeature: 'Bermas (trnsito peatonal)', controlMeasure: '', observation: '' },
		{ missingFeature: 'Pretiles contorno caseta', controlMeasure: '', observation: '' },
		{ missingFeature: 'Accesos vehculos y personal', controlMeasure: '', observation: '' },
		{ missingFeature: 'Pretiles contorno', controlMeasure: '', observation: '' },
		{ missingFeature: 'Nivelacin plataforma', controlMeasure: '', observation: '' },
		{ missingFeature: 'Estacionamientos', controlMeasure: '', observation: '' },
		{ missingFeature: 'Pretil contorno y tope bao', controlMeasure: '', observation: '' },
	],
	requiresGeotechnicalEvaluation: '',
	materialRisk: 'Accidente a personas por falla de terreno',
	criticalControl: [
		{ key: 1, label: 'Sistema Control de Terreno', answer: 'No' },
		{ key: 2, label: 'Ejecucin / Operacin y Cumplimiento del Diseo Geotcnico', answer: 'No' },
		{ key: 3, label: 'Segregacin de reas', answer: 'No' },
	],
	platformSignatures: [
		{ area: 'Entrega', responsibles: [{ personaName: '', signed: false, position: 0, signature: '' }] },
		{ area: 'Recibe', responsibles: [{ personaName: '', signed: false, position: 0, signature: '' }] },
	],
};

export function platformDeliveryReducer(state = INITIAL_STATE, { type, payload }) {
	switch (type) {
		case '@platform/updateLoading':
			return { ...state, loading: payload };
		case '@platform/updateRootProps':
			//recuperation, dip, azimuth, depth, sector
			const { prop, value } = payload;

			state[prop] = value;
			return { ...state };

		case '@platform/setControlMeasure':
			return { ...state, controlMeasures: payload };

		case '@platform/updateRevisionProp':
			const { value: valuerevision, index: indexrevision } = payload;
			const revisions = state.revisions;

			revisions[indexrevision]['answer'] = valuerevision;
			return { ...state, revisions };

		case '@platform/addControlMeasure':
			return { ...state, controlMeasures: [...state.controlMeasures, { missingFeature: '', controlMeasure: '', observation: '' }] };

		case '@platform/updateControlMeasureProp':
			const { prop: propcontrolmeasure, value: valuecontrolmeasure, index: indexcontrolmeasure } = payload;
			let controlMeasures = state.controlMeasures;

			controlMeasures[indexcontrolmeasure][propcontrolmeasure] = valuecontrolmeasure;
			return { ...state, controlMeasures };

		case '@platform/updateCriticalControlProp':
			const { value: valuecriticalcontrol, index: indexcriticalcontrol } = payload;
			let criticalControl = state.criticalControl;

			criticalControl[indexcriticalcontrol]['answer'] = valuecriticalcontrol;
			return { ...state, criticalControl };

		case '@platform/updateSignatureProps':
			const { areaIndex: areaindexupd, responsibleIndex: responsibleindexupd, prop: propupd, value: valueupd } = payload;
			const signArray = state.platformSignatures;

			signArray[areaindexupd].responsibles[responsibleindexupd][propupd] = valueupd;
			return { ...state, platformSignatures: signArray };

		case '@platform/setModalConfig':
			return { ...state, modalConfig: { ...state.modalConfig, ...payload } };
		default:
			return state;
	}
}
