import React from 'react';
import TagOriginal from '@outlier-spa/mine/dist/components/Tag';
import './styles.scss';

function Tag({ children, colorTranslucence, ...rest }) {
	return (
		<TagOriginal size='small' rounded colorTranslucence={colorTranslucence} {...rest}>
			{children}
		</TagOriginal>
	);
}

export default Tag;
