import { TAB_INDEX } from 'pages/RequestArea/utils';
import indexDB from 'utils/db';

const INITIAL_STATE = {
	currentAreaHoles: [],
	currentAreaRequest: { requestAreaId: -1, holes: [] },
	tab: TAB_INDEX.REQUEST,
	platformDelivery: [],
	heps: [],
	hepSync: false,
	loading: true,
	platformDeliverySync: false,
	platformSelected: {},
	installed: false,
	areaRequests: [],
	areaRequest: {},
	offline: false,
};

export function baseReducer(state = INITIAL_STATE, { type, payload }) {
	switch (type) {
		// guarda los sondajes de la solicitud seleccionada.
		case '@base/updateHoles':
			const currentAreaRequest = state.currentAreaRequest;
			currentAreaRequest.holes = payload;

			return { ...state, currentAreaRequest: currentAreaRequest };

		// Sobreescribe los datos de la solicitud
		case '@base/updateCurrentAreaRequest':
			return { ...state, currentAreaRequest: { ...state.currentAreaRequest, ...payload } };

		case '@base/updateRootProp':
			const { prop, value } = payload;
			state[prop] = value;
			return { ...state };

		case '@base/addPlatform':
			return { ...state, platformDelivery: [...state.platformDelivery, ...payload] };

		case '@base/replacePlatform':
			return { ...state, platformDelivery: payload };

		case '@base/replaceHep':
			return { ...state, heps: payload };

		case '@base/updatePlatform':
			const { holeid, arearequestid, prop: propPlatform, value: valuePlatform } = payload;
			const platformDelivery = state.platformDelivery;
			const find = platformDelivery.find((p) => p.holeid === holeid);
			find[propPlatform] = valuePlatform;
			indexDB.baseReducer.add({ ...state, platformDelivery: platformDelivery });
			return { ...state, platformDelivery: platformDelivery };

		// area request (listado de todas las solicitudes)
		case '@base/cleanAreaRequests':
			return { ...state, areaRequests: [] };
		case '@base/addAreaRequests':
			return { ...state, areaRequests: [...state.areaRequests, payload] };
		case '@base/replaceAreaRequests':
			return { ...state, areaRequests: payload };

		// Solicitud de rea seleccionada
		case '@base/addRequest':
			return { ...state, areaRequest: payload };

		// Entrega plataforma seleccionada
		case '@base/updatePlatformSelected':
			return { ...state, platformSelected: payload };

		case '@base/updateAll':
			return { ...state, ...payload };
		default:
			return state;
	}
}
