export const requestExtensionValidation = {
	checkEmpty: (request) => {
		var errors = {};
		(!request.dateExtensionAuthorization || request.dateExtensionAuthorization.from === '' || request.dateExtensionAuthorization.from === undefined) &&
			(errors['extensionFrom'] = 'Requerido');

		(!request.dateExtensionAuthorization || request.dateExtensionAuthorization.to === '' || request.dateExtensionAuthorization.to === undefined) &&
			(errors['extensionTo'] = 'Requerido');

		(!request.reasonExtension || request.reasonExtension.trim() === '') && (errors['reasonExtension'] = 'Motivo es Requerido');

		request.extensionSignatures &&
			request.extensionSignatures.forEach((sign) => {
				sign.responsibles.forEach((resp, respIndex) => {
					(!resp.personaName || resp.personaName.trim() === '') && (errors[`personaName-${sign.area}-${respIndex}`] = 'Nombre es Requerido');
				});
			});

		const containError = !!Object.keys(errors).length;
		return { data: request, containError: containError, errors };
	},
};
