export const INITIAL_STATE = {
	modalConfig: {},
	returnObservation: null,
	withoutSignature: false,
	file: undefined,
	returnSignatures: [
		{ area: 'Devolucin', responsibles: [{ personaName: null, signed: false, position: 0, signature: null }] },
		{ area: 'Recibe conforme', responsibles: [{ personaName: null, signed: false, position: 0, signature: null }] },
	],
	requestReturnId: -1,
	returnImage: null,
};

export function requestReturnFormReducer(state = INITIAL_STATE, action) {
	switch (action.type) {
		case '@returnForm/addSignature':
			const { areaIndex: addAreaIndex, position: positionSignature } = action.payload;
			let returnSignatures = state.returnSignatures;

			returnSignatures[addAreaIndex].responsibles = [
				...returnSignatures[addAreaIndex].responsibles,
				{ personaName: '', signed: false, position: positionSignature, signature: '' },
			];
			return { ...state, returnSignatures };
		case '@returnForm/removeSignature':
			const { areaIndex: airemove, responsibleIndex: riremove } = action.payload;
			let signs = state.returnSignatures;
			let responsiblesFilter = signs[airemove].responsibles.filter((_, k) => k !== riremove);

			signs[airemove].responsibles = responsiblesFilter;
			return { ...state, returnSignatures: signs };

		case '@returnForm/updateSignatureProps':
			const { areaIndex: areaindexupd, responsibleIndex: responsibleindexupd, prop: propupd, value: valueupd } = action.payload;
			let signArray = state.returnSignatures;

			signArray[areaindexupd].responsibles[responsibleindexupd][propupd] = valueupd;
			return { ...state, returnSignatures: signArray };

		case '@returnForm/setModalConfig':
			return state.modalConfig
				? { ...state, modalConfig: { ...state.modalConfig, ...action.payload } }
				: { ...state, modalConfig: { ...action.payload } };

		case '@returnForm/updateRootProp':
			const { prop, value } = action.payload;

			state[prop] = value;
			return { ...state };

		case '@returnForm/updateAll':
			return { ...state, ...action.payload };

		default:
			return state;
	}
}
