import Loading from '@outlier-spa/loading';
import { DownloadFilled, PdfTone } from '@outlier-spa/mine-icon/dist/icons';
import Input from '@outlier-spa/mine/dist/components/Input';
import { Section } from '@outlier-spa/mine/dist/components/Section';
import { Table } from 'antd';
import { BaseLayout } from 'App';
import { HeaderPage } from 'components/Headers';
import useDebouncedEffect from 'hooks/useDebouncedEffect';
import _ from 'lodash';
import React, { useCallback, useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { baseApiUrl, db } from 'utils/database';
import { HepDetails } from './Details';
import { HepForm } from './Form';
import { HepStyle, TableStyles } from './styled';
import { INITIAL_STATE, hepReducer } from 'reducer/hepReducer';
import './styles.scss';

export function updateProp(prop, value) {
	return { type: '@hep/updateRootProps', payload: { prop: prop, value: value } };
}

export function updateHepProp(prop, value) {
	return { type: '@hep/updateHepData', payload: { prop, value } };
}

export const _sortColumns = (a, b, key = null) => {
	if (!key && (typeof a === 'object' || typeof b === 'object')) return false;
	return (a[key] || a).toString().localeCompare((b[key] || a).toString());
};

export const _hepColumns = [
	{
		key: 'holeId',
		title: 'HOLEID',
		dataIndex: 'holeId',
		sorter: (a, b) => _sortColumns(a, b, 'holeId'),
		width: 180,
		fixed: 'left',
	},
	{
		key: 'Nombre_Propuesto',
		title: 'Propuesto',
		dataIndex: 'Nombre_Propuesto',
		sorter: (a, b) => _sortColumns(a, b, 'Nombre_Propuesto'),
		width: 180,
		fixed: 'left',
	},
	{
		key: 'Prioridad_Sondaje',
		title: 'Prioridad',
		dataIndex: 'Prioridad_Sondaje',
		width: 110,
		sorter: (a, b) => _sortColumns(a, b, 'Prioridad_Sondaje'),
	},
	{
		key: 'Nombre_Diseno',
		title: 'Diseo',
		dataIndex: 'Nombre_Diseno',
		width: 180,
		sorter: (a, b) => _sortColumns(a, b, 'Nombre_Diseno'),
	},
	{
		key: 'FiscalYear_Propuesto',
		title: 'FY',
		dataIndex: 'FiscalYear_Propuesto',
		width: 70,
		sorter: (a, b) => _sortColumns(a, b, 'FiscalYear_Propuesto'),
	},
	{
		key: 'Campana',
		title: 'Campaa',
		dataIndex: 'Campana',
		width: 220,
		sorter: (a, b) => _sortColumns(a, b, 'Campana'),
	},
	{
		key: 'hep',
		title: 'HEP',
		dataIndex: 'hep',
		width: 240,
		render: (_, row) => renderHepColumn(row),
	},
];

export const _hepDefaultTypes = { RC: 'SPRC', DDH: 'SPD' };

function AllHeps(props) {
	const { holes, hepData } = useSelector((selector) => selector.hepReducer);
	const dispatch = useDispatch();
	const [loading, setLoading] = useState(false);
	const [search, setSearch] = useState(null);

	const handleChangeHoleId = (hole) => {
		if (!hole || !hole.holeId) return;
		db.get(`Hep/hole/${hole.holeId}`).then((response) => {
			if (response.data) {
				const { data } = response;
				var holeDetails = _.merge(hole, data);

				if (!holeDetails.type && holeDetails['Objetivo_Tipo']) {
					holeDetails.type = _hepDefaultTypes[holeDetails['Objetivo_Tipo']] || null;
				}

				dispatch(updateProp('hepData', holeDetails));
			}
		});
	};

	const updateRootProps = useCallback(
		(prop, value) => {
			dispatch(updateProp(prop, value));
		},
		[dispatch]
	);

	const getHoles = useCallback(
		(text) => {
			if (!text) {
				updateRootProps('holes', []);
				updateRootProps('hepData', {});
				return;
			}
			// else if (text.length < 4) return;

			setLoading(true);
			db.get(`Hep/BySearch/${text}`)
				.then((response) => {
					if (response.data) {
						const { data } = response;
						updateRootProps(
							'holes',
							data.map((d) => ({ ...d, key: d['Nombre_Propuesto'] }))
						);
					}
				})
				.finally(() => setLoading(false));
		},
		[updateRootProps]
	);

	useEffect(() => {
		dispatch({ type: 'clean' });
	}, []);

	useDebouncedEffect(
		() => {
			if (search === null) return;
			getHoles(search);
		},
		500,
		[search]
	);

	return (
		<BaseLayout className='all-heps_page' onChangeTheme={props.onChangeTheme} light={props.light}>
			<Loading active={loading} />

			<HeaderPage style={{ padding: '19px 0' }}>HEP</HeaderPage>
			<HepStyle className='hep-page flex flex-column flex-1'>
				<div className='flex flex-gap-14 flex-1' style={{ height: '100%', maxWidth: '100%', maxHeight: 'calc(100vh - 220px)' }}>
					<div className='flex-vgap-14 hep-page_holes'>
						<div style={{ minHeight: 500 }} className='flex flex-vgap-14 hep-page_list'>
							<Input value={search} onChange={setSearch} placeholder='Escriba el nombre del sondaje para buscar...' />
							<Section style={{ height: 'calc(100% - 44px)' }}>
								<TableStyles className='table-hep'>
									<Table
										columns={_hepColumns}
										dataSource={holes}
										pagination={false}
										scroll={{ x: '100%', y: 450 }}
										locale={{ triggerDesc: 'Descendente', triggerAsc: 'Ascendente', cancelSort: 'Cancelar orden' }}
										onRow={(item, _rowIndex) => {
											return {
												onClick: () => handleChangeHoleId(item),
											};
										}}
									/>
								</TableStyles>
							</Section>
						</div>

						<Section className='hep-page_details'>
							<TableStyles>
								<HepDetails data={hepData.holeId ? [{ ...hepData, key: 1 }] : []} />
							</TableStyles>
						</Section>
					</div>

					<Section className='flex-gap-14 inline-fields' style={{ width: 288 }}>
						<HepForm
							onSave={() => {
								updateRootProps('hepData', {});
								getHoles(search);
							}}
						/>
					</Section>
				</div>
			</HepStyle>
		</BaseLayout>
	);
}

export default AllHeps;

// Renderizado personalizado de la celda HEP
function renderHepColumn(props) {
	const { fileName, holeId } = props;
	return fileName ? (
		<p style={{ paddingLeft: 30, position: 'relative' }}>
			<PdfTone style={{ width: 18, height: 18, position: 'absolute', left: 0, top: '50%', transform: 'translateY(-50%)' }}></PdfTone>
			<a href={`${baseApiUrl}hep/pdf/${holeId}`} target='_blank' rel='noreferrer'>
				{fileName}
				<DownloadFilled style={{ position: 'absolute', right: 0, top: '50%', transform: 'translateY(-50%)' }} />
			</a>
		</p>
	) : (
		<span style={{ opacity: 0.5 }}>No existe documento</span>
	);
}
