import { REQUEST_STATE } from 'pages/RequestArea/utils';

export const INITIAL_STATE = {
	name: '',
	restriction: '',
	sector: '',
	sectors: [],
	date: null,
	rgoFile: null,
	rgoId: -1,
	rgoName: '',
	errors: {},
	filePreview: null,
	modalConfig: { content: null },
	loading: true,
	state: REQUEST_STATE.NO_CREATED,
};

export function rgoReducer(state = INITIAL_STATE, action) {
	switch (action.type) {
		case '@rgo/updateRootProps':
			const { prop, value } = action.payload;

			state[prop] = value;

			return { ...state };

		case '@rgo/addProps':
			return { ...state, ...action.payload };

		case '@rgo/updateLoading':
			return { ...state, loading: action.payload };

		case '@rgo/updateModalConfig':
			return { ...state, modalConfig: { ...action.payload } };
		default:
			return state;
	}
}
