export const INITIAL_STATE = {
	modalConfig: {},
	loading: false,
	sector: '',
	startDate: null,
};

export function newRequestReturnReducer(state = INITIAL_STATE, action) {
	switch (action.type) {
		case '@newReturn/setModalConfig':
			return state.modalConfig
				? { ...state, modalConfig: { ...state.modalConfig, ...action.payload } }
				: { ...state, modalConfig: { ...action.payload } };

		case '@newReturn/updateRootProp':
			const { prop, value } = action.payload;

			state[prop] = value;
			return { ...state };

		default:
			return state;
	}
}
