import Popover from '@outlier-spa/mine/dist/components/Popover';
import { RootStyles } from '@outlier-spa/mine/dist/components/styled';
import themes from '@outlier-spa/mine/dist/config/themes';
import AuthLayout from 'components/AuthLayout';
import NotFound from 'components/NotFound';
import ProfileNav from 'components/ProfileNav';
import { useOfflineStatus } from 'hooks/useOfflineStatus';
import Hep from 'pages/Hep';
import AllHeps from 'pages/Hep/AllHeps';
import PlatformDelivery from 'pages/PlatformDelivery';
import RequestAreaIndex from 'pages/RequestArea';
import NewRequest from 'pages/RequestArea/NewRequest';
import NewReturnRequest from 'pages/RequestReturn/NewReturnRequest';
import RequestBase from 'pages/RequestArea/RequestBase';
import NewExtensionRequest from 'pages/RequestExtension/NewExtensionRequest';
import Rgo from 'pages/Rgo';
import { useEffect, useState } from 'react';
import { QueryClient, QueryClientProvider } from 'react-query';
import { Provider, useSelector } from 'react-redux';
import { HashRouter, Redirect, Route, Switch, useHistory } from 'react-router-dom';
import { baseReducer } from 'reducer/baseReducer';
import { hepReducer } from 'reducer/hepReducer';
import { newRequestExtensionReducer } from 'reducer/newRequestExtensionReducer';
import { newRequestReturnReducer } from 'reducer/newRequestReturnReducer';
import { requestExtensionFormReducer } from 'reducer/requestExtensionFormReducer';
import { requestFormReducer } from 'reducer/requestFormReducer';
import { requestReducer } from 'reducer/requestReducer';
import { requestReturnFormReducer } from 'reducer/requestReturnFormReducer';
import { rgoReducer } from 'reducer/rgoReducer';
import { combineReducers, createStore } from 'redux';
import { FooterStyle, GlobalStyle, HeaderPage, MenuCustom, MenuItemSingle, SideNavStyle, Wrapper } from 'styled';
import { ThemeProvider } from 'styled-components';
import 'styles/common.scss';
import { theme } from 'theme';
import 'utils/database';
import { db } from 'utils/database';
import indexDB from 'utils/db';
import './App.css';
import useWebInstallPrompt from 'hooks/useWebInstallPrompt';

