const store = createStore(
	combineReducers({
		baseReducer,
		newRequestReturnReducer,
		// newRequestExtensionReducer,
		rgoReducer,
		requestReducer,
		requestFormReducer,
		requestExtensionFormReducer,
		requestReturnFormReducer,
		hepReducer,
	})
);

var customTheme = { light: { ...themes.light, ...theme.light }, dark: { ...themes.dark, ...theme.dark } };

const showMenu = (show) => {
	var sidenav = document.querySelector('#app-wrapper>.sidenav');
	if (sidenav) {
		if (show) sidenav.classList.add('active');
		else sidenav.classList.remove('active');
	}
};

export const BaseLayout = ({ title, light, className, showHelp = false, onToggleTheme, onChangeTheme, cleanInner, children }) => {
	const offline = useSelector((selector) => selector.baseReducer.offline);

	return (
		<>
			<HeaderPage className='header'>
				<BurgerMenuOutlined onClick={() => showMenu(true)} />
				{title}
				{showHelp && (
					<a className='help-header' href='https://drillingworkflow.spence.aks.odev.cl/' rel='noreferrer' target='_blank'>
						<HelpTone></HelpTone> <span>Ayuda</span>
					</a>
				)}
				<Popover trigger={offline ? 'hover' : 'trigger'} content={offline && 'Algunas caracteristicas podrian no estar disponibles'}>
					<span style={{ cursor: offline ? 'pointer' : 'normal' }}>{offline ? 'Offline' : ''} </span>
				</Popover>
				<ProfileNav onToggleTheme={onToggleTheme} light={light} onChangeTheme={onChangeTheme} />
			</HeaderPage>
			<div className='content'>
				<div className={`inner-content ${cleanInner ? 'clean-inner' : ''} ${className ? className : ''}`}>{children}</div>
				<FooterStyle className='footer sticky bottom0'></FooterStyle>
			</div>
		</>
	);
};

async function sendPlatformToApi(platforms) {
	return await db.post('PlatformDelivery/save/offline', platforms, { contentType: 'application/json' });
}

async function sendHepToApi(heps) {
	return await db.post(`Hep/save/offline`, heps, { contentType: 'application/json' });
}

const AppLayout = (props) => {
	useOfflineStatus();
	const offline = useSelector((selector) => selector.baseReducer.offline);
	const history = useHistory();
	const redirect = (value) => {
		history.push(value);
		showMenu(false);
	};

	useEffect(() => {
		if (!offline) {
			async function SendToApi() {
				var platforms = await indexDB.table('platformDeliveryQueue').toArray();
				var heps = await indexDB.table('hepQueue').toArray();

				// SI existen entrega de plataforma en cola
				if (platforms.length > 0) {
					sendPlatformToApi(platforms).then(({ status, data }) => {
						if (status === 200 && data?.state === true) {
							indexDB.table('platformDeliveryQueue').clear();
						}
					});
				}

				if (heps.length > 0) {
					sendHepToApi(heps).then(({ status, data }) => {
						if (status === 200 && data?.state === true) {
							indexDB
								.table('hepCache')
								.toArray()
								.then((resp) => {
									heps.forEach((val) => {
										const find = resp.find((h) => h.holeId === val.holeId);
										if (find !== undefined) {
											indexDB.table('hepCache').delete(find.id);
										}
									});
								})
								.finally(() => {
									indexDB.table('hepQueue').clear();
								});
						}
					});
				}
			}
			SendToApi();
		}

		return offline;
	}, [offline]);

	return (
		<AuthLayout>
			<Wrapper id='app-wrapper' className='app-wrapper'>
				<RootStyles />
				<GlobalStyle />
				<SideNavStyle className='sidenav'>
					<InstallPWA />
					<div className='header'>
						<span>Drilling Workflow</span>
					</div>
					<div className='content'>
						<MenuCustom defaultSelectedKeys={['request']}>
							<MenuItemSingle key='request' onClick={() => redirect('/request-area')}>
								Solicitud de rea
							</MenuItemSingle>
							<MenuItemSingle key='hep' onClick={() => redirect('/hep')}>
								HEP
							</MenuItemSingle>
						</MenuCustom>
					</div>
				</SideNavStyle>
				<div className='overlay' onClick={(_) => showMenu(false)}></div>
				<div className='dynamic-container'>{props.children}</div>
			</Wrapper>
		</AuthLayout>
	);
};

const queryClient = new QueryClient();

function App() {
	const [isLight, setIsLight] = useState(false);
	const themeProps = { light: isLight, onChangeTheme: setIsLight };

	return (
		<>
			<HashRouter>
				<Provider store={store}>
					<QueryClientProvider client={queryClient}>
						<ThemeProvider theme={isLight ? customTheme.light : customTheme.dark}>
							<div id='app-preload' className='app-preload'>
								<span className='preload-animation'></span>
							</div>
							<AppLayout>
								<Switch>
									<Route path='/request-area' render={(props) => <RequestAreaIndex {...props} {...themeProps} />}></Route>
									<Route path='/hep' render={(props) => <AllHeps {...props} {...themeProps} />}></Route>
									<Route path='/request-area-view/:id' render={(props) => <RequestBase {...props} {...themeProps} />}></Route>
									<Route path='/new-request-area' render={(props) => <NewRequest {...props} {...themeProps} />}></Route>
									<Route path='/extension-request/:id' render={(props) => <NewExtensionRequest {...props} {...themeProps} />}></Route>
									<Route path='/return-request/:id' render={(props) => <NewReturnRequest {...props} {...themeProps} mode='new' />}></Route>
									<Route path='/hep' render={(props) => <Hep {...props} {...themeProps} />}></Route>

									<Route path='/no-item' render={(props) => <NotFound {...props} {...themeProps} />}></Route>

									<Route path='/rgo' render={(props) => <Rgo {...props} {...themeProps} />}></Route>
									<Route path='/platform-delivery' render={(props) => <PlatformDelivery {...props} {...themeProps} />}></Route>
									{/* <Route path='/test' component={Test}></Route> */}
									<Route path='/' exact render={(_) => <Redirect to='/request-area' />} />
								</Switch>
							</AppLayout>
						</ThemeProvider>
					</QueryClientProvider>
				</Provider>
			</HashRouter>
		</>
	);
}

export default App;
