import { DetailsOutlined, ImageOutlined, InfoFilled, PdfTone, TrashcanFilled } from '@outlier-spa/mine-icon';
import Button from '@outlier-spa/mine/dist/components/Button';
import Input, { TextArea } from '@outlier-spa/mine/dist/components/Input';
import Modal from '@outlier-spa/mine/dist/components/Modal';
import Popover from '@outlier-spa/mine/dist/components/Popover';
import { Section, SectionHeader } from '@outlier-spa/mine/dist/components/Section';
import SwitchOptions from '@outlier-spa/mine/dist/components/SwitchOptions';
import Upload from '@outlier-spa/mine/dist/components/Upload';
import { TitleInput } from 'components/Common';
import DrawSignature, { defaultProps as defaultDrawSignatureProps } from 'components/DrawSignature';
import SignatureViewer, { SIGNED_TEXT } from 'components/DrawSignature/SignatureViewer';
import Error from 'components/Error';
import { DetailsHeader } from 'components/Headers';
import ImageViewer from 'components/ImageViewer';
import PdfViewer from 'components/PdfViewer';
import React, { useEffect, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { DescriptionText } from 'styled';
import { resizeImage } from 'utils';
import { requestReturnValidation } from './validation';

const imageUrl = `${process.env.REACT_APP_API_URL}RequestReturn/getImage/`;
const documentUrl = `${process.env.REACT_APP_API_URL}RequestReturn/getDocument/`;

const RequestReturnForm = React.forwardRef(({ mode = 'edit', initial, ...rest }, ref) => {
	const { id } = rest.match.params;
	const modalRef = useRef();
	const modalImageRef = useRef();
	const signatureRef = useRef();
	const modalPdfRef = useRef();
	const state = useSelector((selector) => selector.requestReturnFormReducer);
	const dispatch = useDispatch();

	const handleViewPdf = () => {
		modalPdfRef.current.open();
	};

	const handleOpenSignatureModal = (areaIndex, responsibleIndex, prop) => {
		dispatch({ type: '@returnForm/setModalConfig', payload: { title: 'Firma', onSubmit: () => onSubmitModal(areaIndex, responsibleIndex, prop) } });
		modalRef.current.open();
	};

	const onSubmitModal = (areaIndex, responsibleIndex, prop) => {
		signatureRef.current.getBase64Data(null, (uri) => {
			dispatch({ type: '@returnForm/updateSignatureProps', payload: { areaIndex, responsibleIndex, prop, value: uri } });
		});

		dispatch({ type: '@returnForm/updateSignatureProps', payload: { areaIndex, responsibleIndex, prop: 'signed', value: true } });
		modalRef.current.close();
	};

	const handleAddSignature = (areaIndex) => {
		dispatch({ type: '@returnForm/addSignature', payload: { areaIndex, position: state.returnSignatures[areaIndex].responsibles.length } });
	};

	const handleRemoveSignature = (areaIndex, responsibleIndex) => {
		dispatch({ type: '@returnForm/removeSignature', payload: { areaIndex, responsibleIndex } });
	};

	const updateSignatureProps = (areaIndex, responsibleIndex, prop, value) => {
		dispatch({ type: '@returnForm/updateSignatureProps', payload: { areaIndex, responsibleIndex, prop, value } });
	};

	const updateRootProps = (prop, value) => {
		dispatch({ type: '@returnForm/updateRootProp', payload: { prop, value } });
	};

	const changeWithoutSignature = (prop, value) => {
		updateRootProps(prop, value);
		if (value === true) {
			updateRootProps('returnObservation', 'Devolucin sin firmas');
			updateRootProps('errors', {});
		} else {
			updateRootProps('returnObservation', '');
		}
	};

	const handleChangeUpload = async (evt) => {
		const { file } = evt.target.files[0].blobFileStruct();
		resizeImage(file, { w: 640, h: 480 }, 'dataURL', function (uri) {
			dispatch({ type: '@returnForm/updateRootProp', payload: { prop: 'returnImage', value: uri } });
			updateRootProps('imageLoading', false);
		});
	};

	const handleViewImage = () => {
		modalImageRef.current.open();
	};

	// *Validaciones
	const handleCheck = () => {
		const validations = requestReturnValidation.checkEmpty(state, mode);

		dispatch({ type: '@returnForm/updateRootProp', payload: { prop: 'errors', value: validations.errors } });
		return validations;
	};

	useEffect(() => {
		updateRootProps('requestId', id);
	}, []);

	ref && (ref.current = { getState: handleCheck });

	return (
		<>
			<Modal ref={modalRef} size='large' title='Autorizacin y Firma - Planificacin corto plazo' {...state.modalConfig}>
				<div className='pd-14 '>
					<div className='signature-draw-container'>
						<DrawSignature
							ref={signatureRef}
							{...defaultDrawSignatureProps}
							identifier={state.modalConfig?.identifier}
							canvasWidth='100%'
							canvasHeight='80vh'
						/>
					</div>
					<DescriptionText style={{ marginTop: 20 }}>
						La firma realizada en este recuadro, ser utilizada nicamente en este documento, y no ser guardada o manipulada fuera de este.
					</DescriptionText>
				</div>
			</Modal>
			<Modal ref={modalImageRef} size='large' cancelProps={{ text: 'Cerrar' }} okProps={{ visible: false }}>
				<div className='pd-14' style={{ height: '85vh' }}>
					<ImageViewer url={`${imageUrl}${state.requestReturnId}`} wrap></ImageViewer>
				</div>
			</Modal>

			{state.requestReturnId && mode === 'edit' && (
				<Modal ref={modalPdfRef} cancelProps={{ text: 'Cerrar' }} size='large' okProps={{ visible: false }}>
					<PdfViewer url={`${documentUrl}${state.requestReturnId}`}> </PdfViewer>
				</Modal>
			)}

			{mode === 'edit' && (
				<Button prefix={<PdfTone />} onClick={handleViewPdf}>
					Ver devolucin
				</Button>
			)}
			<DetailsHeader icon={<DetailsOutlined />} text='Detalles'></DetailsHeader>

			<div className='flex-vgap-20'>
				<div>
					<div className='flex'>
						<TitleInput>Devolucin sin firmas</TitleInput>
						<Popover trigger='hover' content='Si se selecciona "Si", se crea una solicitud de devolucin de rea sin requerir firmas.'>
							<InfoFilled style={{ marginLeft: 10, lineHeight: '1.375rem', cursor: 'pointer' }} />
						</Popover>
					</div>
					<SwitchOptions
						disabled={mode === 'edit'}
						index={state.withoutSignature ? 0 : 1}
						onChange={(value) => {
							const { content } = value;
							changeWithoutSignature('withoutSignature', content.toLowerCase() === 'si');
						}}></SwitchOptions>
				</div>
				<div>
					<TitleInput>Observaciones</TitleInput>
					<TextArea
						size='large'
						disabled={mode === 'edit' || state.withoutSignature}
						isError={state.errors && state.errors['returnObservation']}
						message={state.errors && state.errors['returnObservation']}
						value={state.returnObservation}
						placeholder='Escriba una observacin'
						onChange={(value) => {
							state.errors && delete state.errors['returnObservation'];
							updateRootProps('returnObservation', value);
						}}
						style={{ width: 720 }}></TextArea>
				</div>
				<div className='request-page'>
					<TitleInput>Fotografa o Plano de rea solicitada</TitleInput>
					{mode !== 'edit' && (
						<Error isError={state.errors && state.errors['returnImage']} message={state.errors && state.errors['returnImage']}>
							{!state.returnImage && (
								<Upload
									disabled={mode === 'edit'}
									multiple
									accept='.png, .jpg, .jpge'
									text={state.imageLoading ? 'Cargando...' : 'Subir Archivo'}
									icon={<ImageOutlined style={{ fontSize: 40 }} />}
									onChange={(value) => {
										state.errors && delete state.errors['returnImage'];
										updateRootProps('imageLoading', true);
										handleChangeUpload(value);
									}}
								/>
							)}
							{state.returnImage && (
								<Section className='flex flex-middle' style={{ height: 380, position: 'relative' }}>
									<TrashcanFilled
										style={{ position: 'absolute', right: 14, top: 14, cursor: 'pointer' }}
										onClick={() => {
											updateRootProps('imageLoading', false);
											dispatch({ type: '@returnForm/updateRootProp', payload: { prop: 'returnImage', value: null } });
										}}></TrashcanFilled>
									<img src={state.returnImage} style={{ height: 380 }} alt=''></img>
								</Section>
							)}
						</Error>
					)}
					{mode !== 'new' && (
						<Section className='image-preview' style={{ height: 380 }}>
							<div
								className='flex flex-middle'
								style={{ height: '100%', cursor: state.returnImageExists && 'pointer' }}
								onClick={() => state.returnImageExists && handleViewImage()}>
								<ImageViewer url={`${imageUrl}${state.requestReturnId}`}></ImageViewer>
							</div>
						</Section>
					)}
				</div>
				{state.returnSignatures?.map((signature, areaIndex) => {
					return (
						<Section style={{ marginBottom: 20 }} key={areaIndex}>
							<SectionHeader left={`Firma ${signature.area}`} />
							<div className='pd-14'>
								{signature.responsibles?.map((ma, responsibleIndex) => {
									return (
										<Section key={responsibleIndex} type='inner' style={{ marginBottom: 14 }}>
											<SectionHeader
												left={`Firma ${responsibleIndex + 1}`}
												right={
													signature.responsibles.length > 1 &&
													mode !== 'edit' && (
														<div className='section-trashcan-container' onClick={() => handleRemoveSignature(areaIndex, responsibleIndex)}>
															<TrashcanFilled />
														</div>
													)
												}
											/>
											<div className='pd-14 signature-section'>
												<TitleInput>Nombres</TitleInput>
												<Input
													size='large'
													disabled={mode === 'edit' || state.withoutSignature}
													isError={state.errors && state.errors[`personaName-${signature.area}-${responsibleIndex}`]}
													message={state.errors && state.errors[`personaName-${signature.area}-${responsibleIndex}`]}
													value={ma.personaName}
													style={{ maxWidth: 560, marginBottom: 20 }}
													placeholder={!state.withoutSignature ? 'Escriba nombres y apellidos' : 'Nombre no requerido'}
													onChange={(value) => {
														state.errors && delete state.errors[`personaName-${signature.area}-${responsibleIndex}`];
														updateSignatureProps(areaIndex, responsibleIndex, 'personaName', value);
													}}
												/>

												<TitleInput>Firma</TitleInput>
												<SignatureViewer
													disabled={ma.signed || state.withoutSignature}
													signed={ma.signed || state.withoutSignature}
													signedText={state.withoutSignature ? 'Firma no requerida' : SIGNED_TEXT}
													onClick={() => handleOpenSignatureModal(areaIndex, responsibleIndex, 'signature')}
												/>
											</div>
										</Section>
									);
								})}
								{mode === 'new' && !state.withoutSignature && (
									<Button type='green' size='large' onClick={() => handleAddSignature(areaIndex)}>
										Aadir integrante
									</Button>
								)}
							</div>
						</Section>
					);
				})}
			</div>
		</>
	);
});

export default RequestReturnForm;
