export const requestReturnValidation = {
	checkEmpty: (request, mode) => {
		var errors = {};

		// Si se crea una devolucion de area sin firmas
		if (request.withoutSignature) {
			return { data: request, containError: false, errors };
		}

		(!request.returnObservation || request.returnObservation.trim() === '') && (errors['returnObservation'] = 'Observacin es requerido');

		request.returnSignatures &&
			request.returnSignatures.forEach((sign) => {
				sign.responsibles.forEach((resp, respIndex) => {
					(!resp.personaName || resp.personaName.trim() === '') && (errors[`personaName-${sign.area}-${respIndex}`] = 'Nombre es Requerido');
				});
			});

		!request.returnImage && mode !== 'edit' && (errors['returnImage'] = 'Requerido');
		const containError = !!Object.keys(errors).length;
		return { data: request, containError: containError, errors };
	},
};
