import React from 'react';
import './styles.scss';
import styled from 'styled-components';
import { Section } from '@outlier-spa/mine/dist/components/Section';
import SwitchOptions from '@outlier-spa/mine/dist/components/SwitchOptions';

const TableDualStyle = styled(Section)`
	&.table-dual-item-container {
		border-bottom: 1px solid ${({ theme }) => theme.fieldBorder};
		.table-dual-item {
			border-bottom: 1px solid ${({ theme }) => theme.fieldBorder};
		}
	}
`;

function SimpleTableCheck({ data, headerProp = { label: 'Item', secondaryLabel: 'Cumple' }, options, onChange, disabled = false }) {
	return (
		<TableDualStyle className='table-dual-item-container'>
			<div className='table-dual-item flex flex-space pd-14'>
				<span className='flex flex-middle'>{headerProp.label}</span>
				<div style={{ width: 138 }}>
					<span className='flex flex-left'>{headerProp.secondaryLabel}</span>
				</div>
			</div>
			{data?.map((d, k) => {
				let indexSelected = 0;
				if (options === undefined) {
					indexSelected = d.answer === 'No' ? 1 : 0;
				} else {
					options.forEach((op, key) => {
						if (op.content === d.answer) {
							indexSelected = key;
						}
					});
				}
				return (
					<div key={k} className='table-dual-item flex flex-space pd-14'>
						<span className='flex flex-middle'>{d.label}</span>
						<span className='flex flex-middle'>
							<SwitchOptions
								disabled={disabled}
								size='large'
								secondary
								index={indexSelected}
								defaultIndex={1}
								options={options}
								onChange={(a, b) => onChange && onChange({ ...a, contentIndex: b, elementPosition: k })}
							/>
						</span>
					</div>
				);
			})}
		</TableDualStyle>
	);
}

export default SimpleTableCheck;
